<template>
    <div class="main">
        <div class="searchDiv">
            <span class="searchDivTitle"></span>
            <div class="searchDivInput">
                <div class="searchShadowTop"></div>
                <div class="searchShadowRight"></div>
                <div class="searchShadowBottom"></div>
                <div class="searchShadowLeft"></div>
                <el-input class="searchInput" v-model="searchStr" id="searchInputId">
                    <i slot="suffix" class="el-input__icon el-icon-search" @click="searchClick"></i>
                </el-input>
                <div id="searchTipId" class="searchTipDiv"></div>
            </div>
            <div class="searchLimitDiv">
                <span class="searchDivTitle" style="margin-left: 10px;"></span>
                <el-input class="searchInput" v-model="searchLimit" style="width: 60px;"></el-input>
            </div>
        </div>
        <div class="showDiv">
            <div class="showRelationDiv">
                <template>
                    <el-table
                            :data="attrTableData"
                            class="customer-table"
                            height="100%"
                            style="width: 100%">
                        <el-table-column
                                prop="entity"
                                label=""
                                align="center">
                        </el-table-column>
                        <el-table-column
                                prop="attribute"
                                label=""
                                align="center">
                        </el-table-column>
                        <el-table-column
                                prop="value"
                                label=""
                                align="center">
                        </el-table-column>
                    </el-table>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "AttributeSearch",
        data(){
            return{
                isInTipDiv:false,
                searchStr:'',
                searchLimit:10,
                attrTableData:[],
                isClick:true,
            }
        },
        watch: {
            'searchStr': function(newVal){
                if(this.isClick){
                    this.isClick=false;
                }else{
                    this.changeTipRequest(newVal);
                }
            }
        },
        beforeDestroy(){
            window.removeEventListener('click',this.windowAddFunction,false);
        },
        mounted(){
            this.$store.commit('updateTitle','');
            this.$store.commit('updateLoginState',true);
            this.startListen();
        },
        methods:{
            startListen(){
                let that=this;
                let tag=document.getElementById('searchInputId');
                tag.addEventListener('click',function (e) {
                    if(that.searchStr.length===0){
                        that.getAttributeList();
                    }
                })
            },
            changeTipRequest(value){
                this.$axios({
                    method:'get',
                    url:'/navigation/search/attribute_autocomplete',
                    params:{
                        entity:value
                    }
                }).then((res)=>{
                    if(res.data.code === 1){
                        if(!(res.data.entity.length&&res.data.entity.length===1&&res.data.entity[0]===value)){
                            this.changeTips(res.data.entity);
                        }
                    }
                })
            },
            changeTips(tipTestData){
                let searchList=tipTestData;
                let str='';
                for(let item of searchList){
                    str+='<div style="width: calc(100% - 10px);min-height: 30px;' +
                        'display: flex;justify-content: flex-start;' +
                        'align-items: center;background-color: rgba(10,135,123,1);z-index: 666">'+ item + '</div>';
                }
                if(searchList.length>0){
                    document.getElementById('searchTipId').style.display='flex';
                    document.getElementById('searchTipId').innerHTML=str;
                    this.setChangeTipStyle();
                }else{
                    document.getElementById('searchTipId').innerHTML='';
                    document.getElementById('searchTipId').style.display='none';
                }
            },
            windowAddFunction(){
                if(!this.isInTipDiv){
                    document.getElementById('searchTipId').innerHTML='';
                    document.getElementById('searchTipId').style.display='none';
                }
            },
            setChangeTipStyle(){
                let that=this;
                window.addEventListener('click',that.windowAddFunction)
                let tag=document.getElementById('searchTipId');
                tag.addEventListener('mouseenter',function (e) {
                    that.isInTipDiv=true;
                })
                tag.addEventListener('mouseout',function (e) {
                    that.isInTipDiv=false;
                })
                let tagList=tag.getElementsByTagName('div');
                for(let item of tagList){
                    item.style.paddingLeft='10px';
                    item.addEventListener('mouseenter',function (e) {
                        item.style.backgroundColor='rgba(20,83,74,1)';
                        item.style.cursor='pointer';
                    })
                    item.addEventListener('mouseout',function (e) {
                        item.style.backgroundColor='rgba(10,135,123,0.7)';
                    })
                    item.addEventListener('click',function (e) {
                        that.isClick=true;
                        that.searchStr=item.innerText;
                        document.getElementById('searchTipId').innerHTML='';
                        document.getElementById('searchTipId').style.display='none';
                    })
                }
            },
            getAttributeList(){
                this.$axios({
                    method:'get',
                    url:'/navigation/search/attribute_autocomplete',
                    params:{
                        entity:''
                    }
                }).then((res)=>{
                    if(res.data.code===1){
                        this.changeTips(res.data.entity);
                    }
                })
            },
            searchClick(){
                this.$axios({
                    method:'get',
                    url:'/navigation/search/attribute_search',
                    params:{
                        entity:this.searchStr,
                        limit:this.searchLimit
                    }
                }).then((res)=>{
                    if(res.data.code===1){
                        // console.log(res.data);
                        this.attrTableData=res.data.triple;
                    }else{
                        this.$message.error('');
                    }
                })
            },
        }
    }
</script>

<style scoped>
    .main{
        height: 100%;
        width: 100%;
    }
    .searchTipDiv{
        border-width: 0 1px 1px 1px;
        border-color: rgba(67,255,226,1);
        border-style: solid;
        color: rgba(67,255,226,1);
        background-color: rgba(10,135,123,1);
        width:450px;
        height:auto;
        max-height: 300px;
        overflow-y: auto;
        font-size: 14px;
        z-index: 999;
        /*display: flex;*/
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        display:none;
    }
    .searchLimitDiv{
        height: 100%;
        width: 150px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .searchDiv{
        height: 37px;
        width: 100%;
        padding-top: 30px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    .searchShadowTop{
        position: absolute;
        top: 0;
        left: 0;
        width: 10px;
        height: 1px;
        background-color: rgba(67,255,226,1);
    }
    .searchShadowRight{
        position: absolute;
        bottom: 0;
        right: 0;
        width: 1px;
        height: 10px;
        background-color: rgba(67,255,226,1);
    }
    .searchShadowBottom{
        position: absolute;
        bottom: 0;
        right: 0;
        width: 10px;
        height: 1px;
        background-color: rgba(67,255,226,1);
    }
    .searchShadowLeft{
        position: absolute;
        top: 0;
        left: 0;
        width: 1px;
        height: 10px;
        background-color: rgba(67,255,226,1);
    }
    .searchDivTitle{
        font-size: 14px;
        color: rgba(67,255,226,1);
        margin-left: 60px;
        margin-right: 10px;
        display: flex;
        justify-content: flex-start;
    }
    .searchDivInput{
        position: relative;
        height: 37px;
        width: 452px;
    }
    .searchInput{
        width: 450px;
        height: 35px;
        line-height: 35px;
        margin-top: 1px;
        margin-left: 1px;
    }
    .searchDiv >>> .el-input__inner{
        height: 35px;
        line-height: 35px;
        background:  -webkit-linear-gradient(left, rgba(20,83,74,1), rgba(5,57,53,0.8));
        padding: 0 0 0 10px;
        color: rgba(67,255,226,1);
        border: 0;
        border-radius: 0;
    }
    .searchDiv >>> .el-input__suffix{
        margin-top: -2px;
        color: rgba(67,255,226,1);
        cursor: pointer;
    }
    .showDiv{
        height: 80%;
        width: 100%;
        display: flex;
        justify-content: center;
        z-index: 400;
    }
    .showRelationDiv{
        width: 80%;
        height: 100%;
        /*margin-left: 60px;*/
        margin-top:20px;
        /*border: 1px solid rgba(67,255,226,1);*/
    }
    .showRelationDiv >>> .el-table__header-wrapper{
        border:1px solid rgba(20,83,74,1);
    }

    .showRelationDiv >>> .el-table__header-wrapper tr th{
        background-color: rgba(20,83,74,1);
        font-size:16px;
        font-weight:500;
        color:rgba(67,255,226,1);
        border: none;
    }
    .showRelationDiv >>> .el-table__body-wrapper{
        background-color: rgba(20,83,74,0.8);
        border:none;
    }
    .showRelationDiv >>> tr{
        background-color: rgba(20,83,74,0.6);
        color: rgba(67,255,226,1);
        border:none;
    }
    .showRelationDiv >>> .el-table--enable-row-hover .el-table__body tr:hover>td{
        background-color: rgba(20,83,74,0.8);
    }
    .showRelationDiv >>> .el-table__body-wrapper tr td{
        border:none;
    }
    .showRelationDiv >>>.el-table__row>td {
        border: none;
    }
    .showRelationDiv >>>.el-table::before {
        height: 0;
    }
</style>