    </div>
</template>

<script>
    import bus from "@/assets/js/bus";

    export default {
        name: "TableShow",
        data(){
            return{
                searchStr:'',
                isTable:false,
                whiteStar:5,
                entityName:'',
                nodes:'',
                attrTableData:[],
                relationTableData:[],
                relationNum:0,
                attrNum:0,
                attrText:'',
                relationText:'',
            }
        },
        mounted(){
            this.$store.commit('updateTitle','');
            this.$store.commit('updateLoginState',true);
            this.entityName =  this.$route.query.node;
            this.tableShow(this.entityName);

        },
        methods:{
            goBack(){
                this.$router.back();
            },
            searchClick(){
                this.tableShow(this.searchStr);
            },
            evaluateClick(index){
                this.whiteStar=5-index;
            },
            submit(){

            },
            tableShow(entity){
                this.$axios({
                    method:'get',
                    url:'/navigation/search/entity_table',
                    params:{
                        entity:entity,
                    }
                }).then((res)=>{
                    if(res.data.code === 1){
                        console.log(res.data)
                        this.attrNum=res.data.attribute.length;
                        this.relationNum=res.data.relationship.length;
                        for(let item of res.data.attribute){
                            this.attrText+=item.rela+":"+item.target+'<br/>';
                            let temp={
                                name:'',
                                value:''
                            }
                            temp.name=item.rela;
                            temp.value=item.target;
                            this.attrTableData.push(temp);
                        }
                        for(let item of res.data.relationship){
                            this.relationText+=item.source+"--"+item.rela+"--"+item.target+'<br/>';
                            let temp={
                                name:'',
                                value:''
                            }
                            temp.name=item.rela;
                            temp.value=item.target;
                            this.relationTableData.push(temp);

                        }
                    }
                })
            },
            showChangeClick(){
                this.isTable=!this.isTable;
            }
        }
    }
</script>

