<template>
    <div class="main">
        <div class="entityDivTop">
            <div class="entityData">
                <img src="../../assets/img/large/3_large.png" class="overViewImage1" alt=""/>
                <span class="entityDivSpan"></span>
                <div class="entityBox">
                    <span class="entityInputSpan">{{overViewData.entity_category}}</span>
                </div>
                <span class="entityDivSpan"></span>

                <span class="entityDivSpan" style="margin-left: 50px;"></span>
                <div class="entityBox">
                    <span class="entityInputSpan">{{overViewData.entity_num}}</span>
                </div>
                <span class="entityDivSpan"></span>

                <span class="entityDivSpan" style="margin-left: 50px;"></span>
                <div class="entityBox">
                    <span class="entityInputSpan">{{overViewData.attribute_num}}</span>
                </div>
                <span class="entityDivSpan"></span>

                <el-button class="downLoad" @click="downLoadClick">
                    <a :href='"/manage/download"' style="color: rgba(67,255,226,1);text-decoration:none;"></a>
                </el-button>
            </div>
            <div class="entityPictureDiv">
                <div class="entityLeftDiv">
                    <div class="leftPicture">
                        <div class="entityDataWordDiv">
                            <span class="entityDataWord">{{overViewData.entity_category}}</span>
                        </div>
                        <img src="../../assets/img/large/5_large.png" class="entityImage" alt=""/>
                        <span class="entityPictureWord"></span>
                    </div>
                </div>
                <div class="entityLeftRightDiv">
                    <i class="el-icon-caret-left" :class="isFirstOne?'sliderButtonGray':'sliderButton'" @click="sliderLeftClick"></i>
                    <div class="myCarouselDiv">
                        <div class="myCarousel">
                            <div v-for="(item,index) in entityShowList" :key="index" class="myCarouselUnit">
                                <img src="../../assets/img/large/object.png" class="overViewEntityImage" alt=""/>
                                <p class="myCarouselP">{{item.name}}</p>
                                <p class="myCarouselPNum">{{item.data}}</p>
                                <el-popover placement="bottom" trigger="click" popper-class="popoverBackB">
                                    <i slot="reference" class="el-icon-warning-outline myCarouselBottom"></i>
                                    <div class="entityDetail">
                                        <p>{{item.detail}}</p>
                                    </div>
<!--                                    <el-button slot="reference">click </el-button>-->
                                </el-popover>
<!--                                <i class="el-icon-warning-outline myCarouselBottom"></i>-->
                            </div>
                        </div>
<!--                        <div class="rightShadowDiv"></div>-->
                    </div>
                    <i class="el-icon-caret-right" :class="isLastOne?'sliderButtonGray':'sliderButton'" @click="sliderRightClick"></i>
                </div>
            </div>
        </div>


        <div class="entityDivCenter">
            <div class="entityData">
                <img src="../../assets/img/large/4_large.png" class="overViewImage2" alt=""/>
                <span class="entityDivSpan"></span>
                <div class="entityBox">
                    <span class="entityInputSpan">{{overViewData.relationship_category}}</span>
                </div>
                <span class="entityDivSpan"></span>
                <span class="entityDivSpan" style="margin-left: 50px;"></span>
                <div class="entityBox">
                    <span class="entityInputSpan">{{overViewData.relationship_num}}</span>
                </div>
                <span class="entityDivSpan"></span>
            </div>
            <div class="entityPictureDiv">
                <div class="entityLeftDiv">
                    <div class="leftPicture">
                        <div class="entityDataWordDiv">
                            <span class="entityDataWord">{{overViewData.relationship_category}}</span>
                        </div>
                        <img src="../../assets/img/large/5_large.png" class="entityImage" alt=""/>
                        <span class="entityPictureWord"></span>
                    </div>
                </div>
                <div class="entityLeftRightDiv" style="height: 80%">
                    <div class="myCarouselDiv">
                        <div class="myCarousel" style="margin-left: 40px;">
                            <div v-for="(item,index) in relationList" :key="index" class="myCarouselRelationUnit">
                                <img src="../../assets/img/large/link.png" class="overViewEntityImage2" alt=""/>
                                <p class="myCarouselP" style="margin-top: 10px">{{item.name}}</p>
                                <p class="myCarouselPNum" style="word-wrap: break-word;text-align: left;padding: 0 10px">{{item.detail}}</p>
                                <div style="position: absolute;width: 100%;bottom: 20px;left: 0;display: flex;justify-content: center;align-items: center;">
                                    <p class="myCarouselPNum2">{{item.number}}</p>
                                </div>
                                <el-popover placement="bottom" trigger="click" popper-class="popoverBackB">
                                    <img slot="reference" src="../../assets/img/large/subLink_normal.png" class="myCarouselBottom" alt=""/>
<!--                                    <i slot="reference" class="el-icon-warning-outline myCarouselBottom"></i>-->
                                    <div class="entityDetail">
                                        <p>{{item.relation}}</p>
                                    </div>
                                </el-popover>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="entityDivBottom">
            <span class="entityInputSpan">{{timeData}}</span>
        </div>
    </div>
</template>

<script>
    // import echarts from 'echarts'
    export default {
        name: "OverView",
        data(){
            return{
                overViewData:{
                    entity_category:'',
                    entity_num:'',
                    relationship_category:'',
                    relationship_num:'',
                    attribute_num:'',
                },
                timeData:'',
                flag:0,
                entityOption:{

                },
                positionList:[],
                leftPos:0,
                rightPos:6,
                isFirstOne:true,
                isLastOne:false,
                entityDataList:[],
                entityShowList:[
                    {
                        name:'',
                        data:'',
                        detail:'',
                    },
                    {
                        name:'',
                        data:'',
                        detail:'',
                    },
                    {
                        name:'',
                        data:'',
                        detail:'',
                    },
                    {
                        name:'',
                        data:'',
                        detail:'',
                    },
                    {
                        name:'',
                        data:'',
                        detail:'',
                    },
                    {
                        name:'',
                        data:'',
                        detail:'',
                    },
                    {
                        name:'',
                        data:'',
                        detail:'',
                    },
                ],
                relationList:[
                    {
                        name:'',
                        detail:'',
                        number:'',
                        relation:''
                    },
                    {
                        name:'',
                        detail:'',
                        number:'',
                        relation:''
                    },
                    {
                        name:'',
                        detail:'',
                        number:'',
                        relation:''
                    },
                    {
                        name:'',
                        detail:'',
                        number:'',
                        relation:''
                    },
                    {
                        name:'',
                        detail:'',
                        number:'',
                        relation:''
                    }
                ],
            }
        },
        mounted(){
            this.$store.commit('updateLoginState',true);
            this.$store.commit('updateTitle','');
            this.getOverViewData();
            // this.updateSliderData();
            // if(!this.$store.state.app.hasSend){
            //     this.$store.commit('updateHasSend',true);
            //     this.getOverViewData();
            // }
        },
        // beforeDestroy(){
        //     this.$store.commit('updateHasSend',false);
        // },
        methods:{
            downLoadClick(){
                console.log(window.location.origin);
                // window.location.href=window.location.origin+'/download';
            },
            updateSliderData(){
                let temp=[];
                // let num=0;
                for(let item of this.entityDataList){
                    if(item.show){
                        temp.push(item);
                        // num++;
                    }
                }
                this.entityShowList=temp;
            },
            sliderLeftClick(){
                if(!this.isFirstOne){
                    let len=this.entityDataList.length-1;
                    if(this.leftPos===0){
                        this.isFirstOne=true;
                    }else{
                        this.entityShowList.pop();
                        this.entityDataList[this.rightPos].show=!this.entityDataList[this.rightPos].show;
                        this.leftPos--;
                        this.rightPos--;
                        this.entityDataList[this.leftPos].show=!this.entityDataList[this.leftPos].show;
                        this.entityShowList.unshift(this.entityDataList[this.leftPos]);
                    }
                    if(this.leftPos===0)this.isFirstOne=true;
                    this.isLastOne = this.rightPos >= len;
                    // this.updateSliderData();
                }
            },
            sliderRightClick(){
                if(!this.isLastOne){
                    let len=this.entityDataList.length-1;
                    if(this.rightPos===len){
                        this.isLastOne=true;
                    }else{
                        this.entityShowList.shift();
                        this.entityDataList[this.leftPos].show=!this.entityDataList[this.leftPos].show;
                        this.leftPos++;
                        this.rightPos++;
                        this.entityDataList[this.rightPos].show=!this.entityDataList[this.rightPos].show;
                        this.entityShowList.push(this.entityDataList[this.rightPos]);
                    }
                    if(this.rightPos===len)this.isLastOne=true;
                    if(this.leftPos>0){
                        this.isFirstOne=false;
                    }
                }
                // this.updateSliderData();
            },
            drawDayLine(){
                let myChart = this.$echarts.init(document.getElementById('chartOne'))
                // 
                myChart.setOption(this.entityOption);
            },
            getOverViewData(){
                this.flag=this.flag+1;
                console.log(this.flag)
                this.$axios({
                    method:'get',
                    url:'/navigation/overview',
                    params:{

                    }
                }).then((res)=>{
                    if(res.data.code === 1){
                        this.overViewData=res.data.data;
                        this.overViewData['attribute_num']=res.data.data.attribute_num;
                        let tempEntity=[];
                        this.leftPos=0;
                        this.rightPos=6;
                        let num=0;
                        for(let item of res.data.data.entity_category_name){
                            let objectTemp=res.data.data.entity_to_attribute[item];
                            let objectList={
                                name:item,
                                data:objectTemp.num,
                                show:num <= 6,
                                detail:'',
                            }
                            let str="";
                            for(let attr of objectTemp.attribute){
                                str+=attr+'';
                            }
                            if(str.length>0)str=str.slice(0,str.length-1)+' '+objectTemp.attribute.length+'';
                            else str='';
                            objectList.detail=str;
                            tempEntity.push(objectList);
                            num++;
                        }
                        let relationTemp=[]
                        for(let item of res.data.data.relationship_category_name){
                            let objectTemp=res.data.data.relacategory_to_relationship[item];
                            let objectList={
                                name:item,
                                detail:objectTemp.text,
                                number:objectTemp.num,
                                relation:'',
                            }
                            let str="";
                            for(let attr of objectTemp.relationship){
                                str+=attr+'';
                            }
                            if(str.length>0)str=str.slice(0,str.length-1)+' '+objectTemp.relationship.length+'';
                            else str='';
                            objectList.relation=str;
                            relationTemp.push(objectList);
                        }
                        this.entityDataList=tempEntity;
                        this.updateSliderData();
                        this.relationList=relationTemp;
                        this.timeData=res.data.data.time+' : '+res.data.data.add_entity+' '+' : '+res.data.data.add_relationship+'';
                    }
                })
            }
        }
    }
</script>

