<template>
    <div class="main">
        <div class="inputDiv">
            <div class="uploadDiv">
                <input type="file" class="inputFile" @change="fileUploadOnSelect" id="input" accept=".txt" @click="fileSelectClick"></input>
                <div class="uploadDivTitle"></div>
            </div>
            <div>
                <span class="wordClass stateDivSpan">:</span>
                <span class="wordClass">{{selectState}}</span>
            </div>
<!--            <el-button class="startBtn"></el-button>-->
        </div>
        <div class="progressDiv">
            <div class="progressClass" v-if="progressFlag">
                <el-progress :percentage="progressPercent" :color="customColor"></el-progress>
            </div>
            <span class="progressSpanClass">{{fileStatus}}</span>
        </div>
        <div class="textDiv">
            <div class="textDivInner">
                <pre>{{textData}}</pre>
<!--                <span>{{textData}}</span>-->
            </div>
        </div>

        <div class="stateDiv">
            <span class="wordClass showDivSpan"></span>
            <span class="wordClass" style="margin-left: 85%;">{{wordLength}}/128</span>
        </div>

        <div class="showDiv">
            <div class="showDivFirst">
<!--                <span class="wordClass showDivSpan"></span>-->
                <el-input v-model="manualInputStr" class="inputStrDiv" type="textarea"></el-input>
            </div>
            <div class="setDiv">
                <div style="width: 80%;height: 100%;position: relative;">
                    <div class="inputEntityUnit">
                        <div class="inputEntityUnitTitle">
                            <span></span>
                        </div>
                        <div class="inputEntityUnitText">
                            <el-input v-model="firstEntityName"></el-input>
                        </div>
                    </div>
                    <div class="lines"></div>
                    <div class="inputEntityUnit" style="left: 22%;">
                        <div class="inputEntityUnitTitle">
                            <span></span>
                        </div>
                        <div class="inputEntityUnitText">
                            <el-input v-model="firstEntityType"></el-input>
                        </div>
                    </div>

                    <div class="lines" style="left: 36%;width: 6%;transform: rotate(32deg);transform-origin: left"></div>
                    <div class="inputEntityUnit" style="left: 41%;top: 40px;width: 18%;">
                        <div class="inputEntityUnitTitle">
                            <span></span>
                        </div>
                        <div class="inputEntityUnitText">
                            <el-input v-model="relationName"></el-input>
                        </div>
                    </div>
                    <div class="lines" style="left: 58%;width: 6%;transform: rotate(-32deg);transform-origin: right"></div>

                    <div class="inputEntityUnit" style="left: 64%;">
                        <div class="inputEntityUnitTitle">
                            <span></span>
                        </div>
                        <div class="inputEntityUnitText">
                            <el-input v-model="secondEntityName"></el-input>
                        </div>
                    </div>
                    <div class="lines" style="left: 78%"></div>
                    <div class="inputEntityUnit" style="left: 80%;">
                        <div class="inputEntityUnitTitle">
                            <span></span>
                        </div>
                        <div class="inputEntityUnitText">
                            <el-input v-model="secondEntityType"></el-input>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="bottomDiv">
            <el-button class="bottomDivBtn" @click="submitSave"></el-button>
<!--            <el-button class="bottomDivBtn"></el-button>-->
        </div>
    </div>
</template>

<script>
    export default {
        name: "RelationManual",
        watch:{
            'manualInputStr': function(newVal){
                this.wordLength=newVal.length;
            }
        },
        data(){
          return{
              customColor:'rgba(10,135,123,1)',
              selectType:'',
              selectTypeList:['',''],
              entitySource:'',
              entityTarget:'',
              relationName:'',
              textData:'',
              manual:'',
              firstEntityName:'',
              firstEntityType:'',
              secondEntityName:'',
              secondEntityType:'',
              fileList: [],
              progressPercent:0,
              progressFlag:false,
              fileStatus:'',
              selectState:'',
              manualInputStr:'',
              wordLength:0,
          }
        },
        mounted(){
            this.$store.commit('updateTitle','');
            this.$store.commit('updateLoginState',true);
        },
        methods:{
            handleChange(file, fileList) {

            },
            handleError(file) {
                this.fileStatus='';
            },
            handleSuccess(file, fileList) {
                this.fileStatus='';
                this.progressPercent=parseInt(file.percentage);
            },
            uploadProgress(event,file,fileList){
                this.fileStatus='';
                this.progressFlag=true;
                this.progressPercent=parseInt(file.percentage);
            },
            fileSelectClick(){
                this.selectState=this.textData?'':'';
            },
            fileUploadOnSelect(){
                // this.textData='';
                const input = document.querySelector('input[type=file]');
                if((input.files[0].size/1024)<500){
                    const reader = new FileReader();
                    reader.readAsText(input.files[0],'utf8') // input.files[0]
                    reader.onload = ()=>{
                        this.selectState='';
                        this.textData = reader.result  // reader.result
                    }
                }else{
                    this.$message.error('500kb');
                }
            },
            submitSave(){
                if(this.manualInputStr&&this.firstEntityName
                    &&this.firstEntityType&&this.secondEntityName&&this.secondEntityType&&this.relationName){
                    if(this.wordLength<=128){
                        this.$axios({
                            method:'post',
                            url:'/manage/mark_triple_submit',
                            headers: {
                                'Content-Type': 'application/x-www-form-urlencoded'
                            },
                            data:{
                                sentence:this.manualInputStr,
                                entity1:this.firstEntityName,
                                classify1:this.firstEntityType,
                                entity2:this.secondEntityName,
                                classify2:this.secondEntityType,
                                relationship:this.relationName
                            }
                        }).then((res)=>{
                            if(res.data.code === 1){
                                this.$message.success('');
                            }else{
                                this.$message.error('');
                            }
                        })
                    }else{
                        this.$message.error('128');
                    }
                }else{
                    this.$message.error('');
                }
            }
        }
    }
</script>

