<template>
    <div class="main">
        <div class="searchDiv">
            <span class="searchDivTitle"></span>
            <div class="searchDivInput">
                <div class="searchShadowTop"></div>
                <div class="searchShadowRight"></div>
                <div class="searchShadowBottom"></div>
                <div class="searchShadowLeft"></div>
                <el-input class="searchInput" v-model="searchStr" id="searchInputId">
                    <i slot="suffix" class="el-input__icon el-icon-search" @click="searchClick"></i>
                </el-input>
                <div id="searchTipId" class="searchTipDiv"></div>
            </div>
            <div class="searchLimitDiv">
                <span class="searchDivTitle" style="margin-left: 10px;"></span>
                <el-input class="searchInput" v-model="searchLimit" style="width: 60px;"></el-input>
            </div>
            <div class="backAndForward">
                <i class="el-icon-arrow-left" :class="isFirstOne?'grayBackBtn':'backBtn'" @click="goBackClick"></i>
                <i class="el-icon-arrow-right" :class="isLastOne?'grayForwardBtn':'forwardBtn'" @click="goForwardClick"></i>
            </div>
        </div>
        <div class="showDiv">
            <KnowledgeMap :entityData="mapData"></KnowledgeMap>
        </div>
        <div class="bottomDiv">
            <el-checkbox-group class="checkBox" v-model="checkList" @change="handleCheckAllChange" v-for="(item,index) in checkNameList">
                <el-checkbox :label="item">{{item}}</el-checkbox>
            </el-checkbox-group>
        </div>
        <div class="newsListDiv" v-show="showTrace">
            <div class="newsListHeader">
                <span></span>
                <div class="newsListHeaderClose" @click="closeNews">
                    <i class="el-icon-close" style="color: rgba(67,255,226,1);"></i>
                </div>
            </div>
            <div class="newsListShow">
                <div class="sourceTextDiv">
                    <div class="sourceTitle" style="justify-content: space-between;">
                        <span></span>
                    </div>
                    <div class="sourceTitle" style="justify-content: space-between;margin-bottom: 5px;">
                        <div style="display: flex;justify-content: flex-start;align-items: center;">
                            <span></span>
                            <div style="width: 10px;height: 10px;background-color: #ff6a6a;margin: 2px 25px 0 5px;"></div>
                            <span></span>
                            <div style="width: 10px;height: 10px;background-color: #f1e986;margin: 2px 25px 0 5px;"></div>
                            <span></span>
                            <div style="width: 10px;height: 10px;background-color: #63b8ff;margin: 2px 25px 0 5px;"></div>
                        </div>
                    </div>
                    <div class="sourceTitle" style="justify-content: space-between;margin-bottom: 25px;">
                        <span style="text-align: left"></span>
                    </div>
                    <div class="sourceTitle">
                        <span></span>
                    </div>
                    <div class="sourceTextShow">
                        <div v-for="item in textValue.text">
                            <span v-if="item.type===0" style="color: whitesmoke">{{item.text}}</span>
                            <span v-if="item.type===1" style="color: #ff6a6a;">{{item.text}}</span>
                            <span v-if="item.type===2" style="color: #63b8ff;">{{item.text}}</span>
                        </div>
                    </div>
                    <div class="sourceTitle">
                        <span></span>
                    </div>
                    <div class="relationTextShow">
                        <span style="color: #ff6a6a">{{textValue.entity1}}</span>
                        <span>--</span>
                        <span style="color: #f1e986">{{textValue.relation}}</span>
                        <span>--</span>
                        <span style="color: #63b8ff">{{textValue.entity2}}</span>
                    </div>
                    <div class="sourceBottomDiv">
                        <div class="sourceTitle" style="margin-left: 0">
                            <span></span>
                        </div>
                        <div class="star">
                            <el-checkbox-group class="checkBox" v-model="traceCheckList" @change="traceChange">
                                <el-checkbox label="0"></el-checkbox>
                                <el-checkbox label="1"></el-checkbox>
                                <el-checkbox label="2"></el-checkbox>
                            </el-checkbox-group>
                        </div>
                        <div class="scoreDiv" v-show="otherState">
                            <span></span>
                            <el-input v-model="score" class="scoreInput"></el-input>
                            <span style="margin-left: 10px">%</span>
                        </div>
                        <div class="submitEvaluate">
                            <el-button class="bottomDivBtn" @click="submit"></el-button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import KnowledgeMap from "@/components/Unit/KnowledgeMap";
    import bus from "@/assets/js/bus";
    export default {
        name: "Search",
        components:{
            KnowledgeMap,
        },
        watch: {
            'searchStr': function(newVal){
                if(this.isClick){
                    this.isClick=false;
                }else{
                    if(newVal!==''){
                        this.changeTipRequest(newVal);
                    }else{
                        document.getElementById('searchTipId').innerHTML='';
                        document.getElementById('searchTipId').style.display='none';
                    }
                }
            },
            'searchLimit':function (newVal) {
                bus.$emit('changeLimit',newVal);
            },
        },
        data(){
          return{
              isInTipDiv:false,
              searchStr:'',
              mapData:[],
              searchLimit:10,
              textValue:{
                  text:'',
                  entity1:'',
                  relation:'',
                  entity2:'',
              },
              checkList:[],
              checkNameList:[],
              otherState:false,
              score:'',
              traceCheckList:[],
              lastSelect:'',
              entityName:'',
              entityName2:'',
              showTrace:false,
              isFirstOne:true,
              isLastOne:true,
              isClick:true,
              // tipTestData:['m1','m2','ak47','','m1','m2','m1','m2','m1','m2','m1','m2','m1','m2','m1','m2','m1','m2','m1','m2'],
          }
        },
        beforeDestroy(){
            this.$store.commit('updateSearchSave',this.searchStr);
            this.$store.commit('updateSelectList',this.checkNameList);
            this.$store.commit('updateSelect',this.checkList);
            window.removeEventListener('click',this.windowAddFunction,false);
        },
        mounted(){
            this.$store.commit('updateTitle','');
            this.$store.commit('updateLoginState',true);
            bus.$emit('changeLimit',this.searchLimit);

            this.myRouter();

            if(this.$store.state.app.globalSearchSave){
                this.searchStr=this.$store.state.app.globalSearchSave;
            }
            if(this.$store.state.app.saveMapList.length&&this.$store.state.app.saveMapList.length>0){
                // console.log('show');
                bus.$emit('showMap',this.$store.state.app.saveMapList[this.$store.state.app.saveMapListPos]);
            }
            this.checkNameList=this.$store.state.app.saveSelectList[this.$store.state.app.saveMapListPos];
            this.checkList=this.$store.state.app.saveSelectListChose[this.$store.state.app.saveMapListPos];

            bus.$off('addCheckList');
            bus.$on('addCheckList',(val)=>{
                // console.log(val)
                this.checkList=[];
                this.checkNameList=[];
                let temp=[];
                let set=new Set(val);
                val=[...set];
                for(let i=0;i<val.length;i++){
                    temp.push(val[i]);
                }
                for(let item of temp){
                    this.checkList.push(item.toString());
                    this.checkNameList.push(item.toString());
                }
                let tempDate=this.$store.state.app.saveSelectList;
                tempDate.push(this.checkNameList);
                this.$store.commit('updateSaveSelectList',tempDate);

                let tempDate2=this.$store.state.app.saveSelectListChose;
                tempDate2.push(this.checkList);
                this.$store.commit('updateSaveSelectListChose',tempDate2);
            })

            bus.$off('showInfoLine');
            bus.$on('showInfoLine',(val)=>{
                // console.log(val);
                this.entityName=val.source.agentName;
                this.entityName2=val.target.agentName;
                this.showTrace=true;
                this.getTraceData(this.entityName,this.entityName2);
            })

            bus.$off('updateRoute');
            bus.$on('updateRoute',()=>{
                this.myRouter();
            })
        },
        methods:{
            closeNews(){
                this.showTrace=false;
            },
            changeTipRequest(value){
                this.$axios({
                    method:'get',
                    url:'/navigation/search/autocomplete',
                    params:{
                        keyword:value
                    }
                }).then((res)=>{
                    if(res.data.code === 1){
                        if(!(res.data.entity.length&&res.data.entity.length===1&&res.data.entity[0]===value)){
                            this.changeTips(res.data.entity);
                        }
                    }
                })
            },
            changeTips(tipTestData){
                let searchList=tipTestData;
                let str='';
                for(let item of searchList){
                    str+='<div style="width: calc(100% - 10px);min-height: 30px;' +
                        'display: flex;justify-content: flex-start;' +
                        'align-items: center;background-color: rgba(10,135,123,0.7)">'+ item + '</div>';
                }
                if(searchList.length>0){
                    document.getElementById('searchTipId').style.display='flex';
                    document.getElementById('searchTipId').innerHTML=str;
                    this.setChangeTipStyle();
                }else{
                    document.getElementById('searchTipId').innerHTML='';
                    document.getElementById('searchTipId').style.display='none';
                }
            },
            windowAddFunction(){
                if(!this.isInTipDiv){
                    document.getElementById('searchTipId').innerHTML='';
                    document.getElementById('searchTipId').style.display='none';
                }
            },
            setChangeTipStyle(){
                let that=this;
                window.addEventListener('click',that.windowAddFunction)
                let tag=document.getElementById('searchTipId');
                tag.addEventListener('mouseenter',function (e) {
                    that.isInTipDiv=true;
                })
                tag.addEventListener('mouseout',function (e) {
                    that.isInTipDiv=false;
                })
                let tagList=tag.getElementsByTagName('div');
                for(let item of tagList){
                    item.style.paddingLeft='10px';
                    item.addEventListener('mouseenter',function (e) {
                        item.style.backgroundColor='rgba(20,83,74,1)';
                        item.style.cursor='pointer';
                    })
                    item.addEventListener('mouseout',function (e) {
                        item.style.backgroundColor='rgba(10,135,123,0.7)';
                    })
                    item.addEventListener('click',function (e) {
                        that.isClick=true;
                        that.searchStr=item.innerText;
                        document.getElementById('searchTipId').innerHTML='';
                        document.getElementById('searchTipId').style.display='none';
                    })
                }
            },
            myRouter(){
                // console.log(this.$store.state.app.saveMapList);
                if(this.$store.state.app.saveMapList.length===0){
                    this.isFirstOne=true;
                    this.isLastOne=true;
                }else{
                    // console.log(this.$store.state.app.saveMapList);
                    if(this.$store.state.app.saveMapListPos===0&&this.$store.state.app.saveMapList.length===1){
                        this.isFirstOne=true;
                        this.isLastOne=true;
                    }else if(this.$store.state.app.saveMapListPos===0&&this.$store.state.app.saveMapList.length!==1){
                        this.isFirstOne=true;
                        this.isLastOne=false;
                    }else if(this.$store.state.app.saveMapListPos===this.$store.state.app.saveMapList.length-1){
                        this.isFirstOne=false;
                        this.isLastOne=true;
                    }else{
                        this.isFirstOne=false;
                        this.isLastOne=false;
                    }
                }
            },
            showCheckList(list,chose){
                this.checkNameList=list;
                this.checkList=chose;
            },
            goBackClick(){
                // console.log(this.$store.state.app.saveMapList);
                if(!this.isFirstOne){
                    let tempPos=this.$store.state.app.saveMapListPos-1;
                    this.$store.commit('updateSaveMapListPos',tempPos);

                    this.$store.commit('updateSelectList',this.$store.state.app.saveSelectList[this.$store.state.app.saveMapListPos]);
                    this.$store.commit('updateSelect',this.$store.state.app.saveSelectListChose[this.$store.state.app.saveMapListPos]);

                    this.showCheckList(this.$store.state.app.saveSelectList[this.$store.state.app.saveMapListPos],this.$store.state.app.saveSelectListChose[this.$store.state.app.saveMapListPos]);

                    this.$store.commit('updateMapSave',this.$store.state.app.saveMapList[this.$store.state.app.saveMapListPos]);
                    this.myRouter();
                    bus.$emit('showMapBack',this.$store.state.app.globalMapSave);
                }
            },
            goForwardClick(){
                if(!this.isLastOne){
                    let tempPos=this.$store.state.app.saveMapListPos+1;
                    this.$store.commit('updateSaveMapListPos',tempPos);

                    this.$store.commit('updateSelectList',this.$store.state.app.saveSelectList[this.$store.state.app.saveMapListPos]);
                    this.$store.commit('updateSelect',this.$store.state.app.saveSelectListChose[this.$store.state.app.saveMapListPos]);

                    this.showCheckList(this.$store.state.app.saveSelectList[this.$store.state.app.saveMapListPos],this.$store.state.app.saveSelectListChose[this.$store.state.app.saveMapListPos]);

                    this.$store.commit('updateMapSave',this.$store.state.app.saveMapList[this.$store.state.app.saveMapListPos]);
                    this.myRouter();
                    bus.$emit('showMapBack',this.$store.state.app.globalMapSave);
                }
            },
            searchClick(){
                // /navigation/search/entity_relationship
                this.$axios({
                    method:'get',
                    url:'/navigation/search/entity_relationship',
                    params:{
                        entity:this.searchStr,
                        limit:this.searchLimit
                    }
                }).then((res)=>{
                    if(res.data.code === 1){
                        this.mapData=res.data.triple;
                        if(res.data.relationship.length===0){
                            let rela=this.mapData[0].rela;
                            this.showCheckList([rela],[rela]);
                            bus.$emit('createMapRelation',this.mapData);
                        }else{
                            let tempDate=this.$store.state.app.saveSelectList;
                            tempDate.push([]);
                            this.$store.commit('updateSaveSelectList',tempDate);

                            let tempDate2=this.$store.state.app.saveSelectListChose;
                            tempDate2.push([]);
                            this.$store.commit('updateSaveSelectListChose',tempDate2);

                            if(this.$store.state.app.saveSelectList.length>0){
                                let tempPos=this.$store.state.app.saveSelectList.length-1;
                                this.$store.commit('updateSaveMapListPos',tempPos);
                                this.showCheckList(this.$store.state.app.saveSelectList[this.$store.state.app.saveMapListPos],this.$store.state.app.saveSelectListChose[this.$store.state.app.saveMapListPos]);
                                this.myRouter();
                            }

                            bus.$emit('createMap',this.searchStr);
                        }
                    }
                })
            },
            handleCheckAllChange(){
                let temp=[];
                for(let i=0;i<this.checkNameList.length;i++){
                    let j=0;
                    for(j;j<this.checkList.length;j++){
                        if(this.checkList[j]===this.checkNameList[i])break;
                    }
                    if(j===this.checkList.length){
                        temp.push(this.checkNameList[i]);
                    }
                }
                console.log(temp);
                bus.$emit('updateMap',temp);
            },
            divisionText(text,source,target){
                let ret=[];
                let normalText=text.split(source);
                for(let index=0;index<normalText.length;index++){
                    let temp=normalText[index].split(target);
                    for(let i=0;i<temp.length;i++){
                        let test=temp[i].split('');
                        for(let item of test){
                            let object={
                                text:item,
                                type:0
                            }
                            ret.push(object);
                        }
                        if(i!==temp.length-1){
                            let test2=target.split('');
                            for(let item of test2){
                                let object={
                                    text:item,
                                    type:2
                                }
                                ret.push(object);
                            }
                        }
                    }
                    if(index!==normalText.length-1){
                        let test3=source.split('');
                        for(let item of test3){
                            let object={
                                text:item,
                                type:1
                            }
                            ret.push(object);
                        }
                    }
                }
                return ret;
            },
            traceChange(value){
                if(value.length>1){
                    this.traceCheckList.shift();
                    this.lastSelect=this.traceCheckList[0];
                }else if(value.length===0){
                    this.traceCheckList.push(this.lastSelect);
                }else{
                    this.lastSelect=this.traceCheckList[0];
                }

                this.otherState = this.traceCheckList[0] === '2';
            },
            submit(){
                let strLen;
                console.log(this.traceCheckList[0]);
                if(this.traceCheckList[0]==='0'){
                    strLen=100;
                }else if(this.traceCheckList[0]==='1'){
                    strLen=0;
                }else{
                    strLen=this.score;
                }
                this.$axios({
                    method:'post',
                    url:'/navigation/search/stars',
                    data:{
                        entity1:this.entityName,
                        entity2:this.entityName2,
                        stars:strLen
                    },
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    },
                }).then((res)=>{
                    if(res.data.code === 1){
                        this.$message.success('');
                    }else{
                        this.$message.error('');
                    }
                })
            },
            getTraceData(tempData,relationData){
                this.traceCheckList=[];
                this.$axios({
                    method:'get',
                    url:'/navigation/search/source',
                    params:{
                        entity1:tempData,
                        entity2:relationData
                    }
                }).then((res)=>{
                    if(res.data.code === 1){
                        let temp=res.data.triple;
                        for(let item of temp){
                            let tempValue={
                                text:[],
                                entity1:'',
                                relation:'',
                                entity2:'',
                            }
                            tempValue.text=this.divisionText(item.text,item.source,item.target);
                            tempValue.entity1=item.source;
                            tempValue.relation=item.rela;
                            tempValue.entity2=item.target;
                            this.textValue=tempValue;
                        }
                        if(res.data.stars!==''&&res.data.stars!==null){
                            this.otherState=false;
                            this.traceCheckList=[];
                            if(res.data.stars===1){
                                this.traceCheckList.push('0');
                            }else if(res.data.stars===0){
                                this.traceCheckList.push('1');
                            }else{
                                this.score=(parseFloat(res.data.stars)*100).toFixed(0);
                                this.traceCheckList.push('2');
                                this.otherState=true;
                            }
                        }
                    }
                })
            },
        }
    }
</script>

