<template>
    <div class="main">
        <div class="sourceClass">
            <span></span>
            <div class="sourceTextDiv">
                <div class="sourceTitle">
                    <div class="sourceTitle1">
                        <span></span>
                    </div>
                    <div class="sourceTitle2">
                        <span></span>
                    </div>
                </div>
                <div class="sourceText">
                    <div v-for="(item,index) in textValue" :key="index" :class="index===textValue.length-1?'textLine2':'textLine'">
                        <div class="sourceText1">
                            <div v-for="item2 in item.text">
                                <span v-if="item2.type===0" style="color: rgba(67,255,226,0.8);">{{item2.text}}</span>
                                <span v-if="item2.type===1" style="color: limegreen;">{{item2.text}}</span>
                                <span v-if="item2.type===2" style="color: orange;">{{item2.text}}</span>
                            </div>
                        </div>
                        <div class="sourceText2">
                            <span style="color: limegreen">{{item.entity1}}</span>
                            <span>--</span>
                            <span style="color: red">{{item.relation}}</span>
                            <span>--</span>
                            <span style="color: orange">{{item.entity2}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="bottomClass">
            <div class="evaluate">
                <span></span>
                <div class="star">
                    <el-checkbox-group class="checkBox" v-model="checkList" @change="handleCheckAllChange">
                        <el-checkbox label="0"></el-checkbox>
                        <el-checkbox label="1"></el-checkbox>
                        <el-checkbox label="2"></el-checkbox>
                    </el-checkbox-group>
                </div>
                <el-button class="bottomDivBtn" style="margin-left: 27%" @click="submit"></el-button>
                <el-button class="bottomDivBtn" style="margin-left: 40px;" @click="goBack"></el-button>
            </div>
        </div>
        <div class="scoreDiv" v-show="otherState">
            <span></span>
            <el-input v-model="score" class="scoreInput"></el-input>
            <span style="margin-left: 10px">%</span>
        </div>
    </div>
</template>

<script>
    import bus from "@/assets/js/bus";

    export default {
        name: "Trace",
        data(){
            return{
                whiteStar:5,
                textValue:[],
                checkList:[],
                nodes:'',
                lines:'',
                searchStr:'',
                entityName:'',
                relationship:'',
                relationValue:'',
                lastSelect:'',
                score:'',
                otherState:false,
                entityName2:'',
            }
        },
        mounted(){
            this.$store.commit('updateTitle','');
            this.$store.commit('updateLoginState',true);
            this.nodes =  JSON.parse(this.$route.query.node);

            this.entityName=this.nodes.agentName;
            if(this.nodes.sendType!=='circle'){
                this.entityName=this.nodes.source.agentName;
                this.entityName2=this.nodes.target.agentName;
                this.relationship=this.nodes.relation.relationship;
            }
            console.log(this.nodes);
            this.getTraceData(this.entityName,this.entityName2);
        },
        methods:{
            goBack(){
                this.$router.back();
            },
            evaluateClick(index){
                this.whiteStar=5-index;
            },
            submit(){
                let strLen;
                if(this.checkList[0]==='0'){
                    strLen=100;
                }else if(this.checkList[0]==='1'){
                    strLen=0;
                }else{
                    strLen=this.score;
                }
                this.$axios({
                    method:'post',
                    url:'/navigation/search/stars',
                    data:{
                        entity1:this.entityName,
                        entity2:this.entityName2,
                        stars:strLen
                    },
                    headers: {
                        'Content-Type': 'application/x-www-form-urlencoded'
                    },
                }).then((res)=>{
                    if(res.data.code === 1){
                        this.$message.success('');
                    }else{
                        this.$message.error('');
                    }
                })
            },
            handleCheckAllChange(value){
                if(value.length>1){
                    this.checkList.shift();
                    this.lastSelect=this.checkList[0];
                }else if(value.length===0){
                    this.checkList.push(this.lastSelect);
                }else{
                    this.lastSelect=this.checkList[0];
                }

                this.otherState = this.checkList[0] === '2';
            },
            getTraceData(tempData,relationData){
                this.$axios({
                    method:'get',
                    url:'/navigation/search/source',
                    params:{
                        entity1:tempData,
                        entity2:relationData
                    }
                }).then((res)=>{
                    if(res.data.code === 1){
                        let temp=res.data.triple;
                        for(let item of temp){
                            let tempValue={
                                text:[],
                                entity1:'',
                                relation:'',
                                entity2:'',
                            }
                            // console.log(item.text);
                            tempValue.text=this.divisionText(item.text,item.source,item.target);
                            tempValue.entity1=item.source;
                            tempValue.relation=item.rela;
                            tempValue.entity2=item.target;
                            this.textValue.push(tempValue);
                        }
                        if(res.data.stars!==''&&res.data.stars!==null){
                            this.otherState=false;
                            if(res.data.stars===1){
                                this.checkList.push('0');
                            }else if(res.data.stars===0){
                                this.checkList.push('1');
                            }else{
                                this.score=(parseFloat(res.data.stars)*100).toFixed(0);
                                this.checkList.push('2');
                                this.otherState=true;
                            }
                        }
                    }
                })
            },
            divisionText(text,source,target){
                let ret=[];
                let normalText=text.split(source);
                for(let index=0;index<normalText.length;index++){
                    let temp=normalText[index].split(target);
                    for(let i=0;i<temp.length;i++){
                        let test=temp[i].split('');
                        for(let item of test){
                            let object={
                                text:item,
                                type:0
                            }
                            ret.push(object);
                        }
                        if(i!==temp.length-1){
                            let test2=target.split('');
                            for(let item of test2){
                                let object={
                                    text:item,
                                    type:2
                                }
                                ret.push(object);
                            }
                        }
                    }
                    if(index!==normalText.length-1){
                        let test3=source.split('');
                        for(let item of test3){
                            let object={
                                text:item,
                                type:1
                            }
                            ret.push(object);
                        }
                    }
                }
                return ret;
            },
        }
    }
</script>

