<template>
    <div class="homeDiv">
        <div class="homeDiv">
            <Slider></Slider>
            <div class="mainDiv" :class='$store.state.app.slider.opened ? "openSlider":"closeSlider"'>
                <div class="main-content" :class="{pc:isPc}">
                    <div class="showDiv">
                        <i class="el-icon-switch-button logout" style="font-size: 24px" @click="logoutClick" title=""></i>
<!--                        <el-button class="logout" @click="logoutClick"></el-button>-->
                        <div class="titleDiv">
                            <div class="titleDivInner">
                                <img src="../../assets/img/large/2_large.png" class="titleImage" alt=""/>
                                <span class="title">{{$store.state.app.headerTitle}}</span>
                            </div>
                        </div>
                        <div class="viceTitleDiv">
                            <div class="line"></div>
                            <div></div>
                        </div>
                        <div class="appMain">
                            <AppMain></AppMain>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import  Slider  from './Slider';
    import  AppMain  from './AppMain';
    // import Login from "@/components/Login/Login";
    import bus from "@/assets/js/bus";

    export default {
        name: "Home",
        components:{
            AppMain,
            Slider,
            // Login,
        },
        computed: {
            key() {
                //router-viewkey 
                return this.$route.name !== undefined ? this.$route.name + +new Date() : this.$route + +new Date()
            }
        },
        data(){
            return{
                isPc:true,
            }
        },
        methods:{
            //
            logoutClick(){
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.logoutFun();
                }).catch(() => {})
            },
            // downLoadClick(){
            //     console.log(window.location.origin);
            //     // window.location.href=window.location.origin+'/download';
            // },
            logoutFun(){
                window.location.href = window.location.origin + '/user/login/loginout';
                // console.log(window.location.origin.toString());
            }
        }
    }
</script>

<style scoped>
    .homeDiv{
        position: relative;
        width: 100%;
        height: 100%;
    }
    .mainDiv{
        position: relative;
        transition: .6s;
        width: 100%;
        min-height: 100%;
    }
    .main-content{
        min-height: 100%;
    }
    .logout{
        position: fixed;
        top: 20px;
        right: 57px;
        background-color: transparent;
        border-color: rgba(67,255,226,1);
        color: rgba(67,255,226,1);
    }
    .logout:hover{
        cursor: pointer;
    }
    .titleDiv{
        height: 25px;
        width: 100%;
        padding-top: 50px;
        margin-top: 0;
    }
    .titleDivInner{
        height: 100%;
        width: 100px;
        margin-left: 60px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        border-bottom: 1px solid rgba(67,255,226,1);
    }
    .titleImage{
        height: 20px;
        width: 20px;
        background-color: transparent;
        margin-right: 10px;
    }
    /*.titleImage:hover{*/
    /*    cursor: pointer;*/
    /*}*/
    .titleIcon{
        font-size: 20px;
        color: #FFFFFF;
        font-weight: 500;
    }
    .title{
        font-size: 16px;
        color: #FFFFFF;
        font-weight: 400;
        margin-left: 5px;
        margin-bottom: -2px;
    }
    .showDiv{
        margin-left: 20px;
        margin-top: 0;
        width: calc(100% - 40px);
        height: 95vh;
    }
    .viceTitleDiv{
        width: 100%;
        height: 25px;
        overflow: hidden;
        /*background-color: #FFFFFF;*/
    }
    .line{
        height: 50px;
        width: 1px;
        background-color: rgba(67,255,226,1);
        margin-left: 60px;
        transform: rotate(40deg);
        transform-origin: 100% 0;
    }
    .appMain{
        height: calc(100% - 90px);
        width: calc(100% - 78px);
        margin-left: 39px;
        border: 1px solid rgba(67,255,226,1);
        overflow: hidden;
    }
    .openSlider{
        left: 190px;
        width: calc(100% - 190px);
    }

    .closeSlider{
        left: 0;
        width: 100%;
    }
    .pc{
        padding-left: 0px;
    }
</style>