<template>
    <div class="main">
        <div class="inputDiv">
<!--            <el-input :disabled="true" class="inputClass" placeholder=""></el-input>-->
            <el-upload
                    class="upload-demo"
                    ref="upload"
                    :action=uploadURL
                    :on-error="handleError"
                    :on-success="handleSuccess"
                    :limit="1"
                    :auto-upload="false"
                    accept=".csv"
                    :file-list="fileList">
                <el-button class="findBtn" @click="inputFile"></el-button>
            </el-upload>
            <el-button class="startBtn" @click="submit"></el-button>
        </div>

        <el-dialog  title="" :visible.sync="isSaving" custom-class="savingDialog"
                    :close-on-click-modal="false"
                    :show-close="false"
                    :close-on-press-escape="false">
            <i class="el-icon-loading" style="color: white;font-size: 30px"></i>
        </el-dialog>

        <el-dialog  title="" :visible.sync="isCalculate" custom-class="savingDialog"
                    :close-on-click-modal="false"
                    :show-close="false"
                    :close-on-press-escape="false">
            <i class="el-icon-loading" style="color: white;font-size: 30px"></i>
        </el-dialog>
<!--        <div class="progressDiv">-->
<!--            <div class="progressClass" v-if="progressFlag">-->
<!--                <el-progress :percentage="progressPercent" :color="customColor"></el-progress>-->
<!--            </div>-->
<!--            <span class="progressSpanClass">{{fileStatus}}</span>-->
<!--        </div>-->
        <div class="showDiv">
            <span class="wordClass showDivSpan"></span>
            <div class="showRelationDiv">
                <template>
                    <el-table
                            :data="attrTableData"
                            class="customer-table"
                            height="100%"
                            style="width: 100%">
                        <el-table-column
                                prop="subject"
                                label=""
                                align="center">
                        </el-table-column>
                        <el-table-column
                                prop="subject_type"
                                label=""
                                align="center">
                        </el-table-column>
                        <el-table-column
                                prop="predicate"
                                label=""
                                align="center">
                        </el-table-column>
                        <el-table-column
                                prop="object"
                                label=""
                                align="center">
                        </el-table-column>
                        <el-table-column
                                prop="object_type"
                                label=""
                                align="center">
                        </el-table-column>
                        <el-table-column
                                prop="text"
                                label=""
                                width="350"
                                align="center">
                        </el-table-column>
                        <el-table-column
                                prop="get_time"
                                label=""
                                align="center">
                        </el-table-column>
                    </el-table>
                </template>
            </div>
        </div>
        <div class="stateDiv">
            <span class="wordClass stateDivSpan">:</span>
            <span class="wordClass">{{fileState}}</span>
            <span class="wordClass stateDivSpan">:</span>
            <span class="wordClass">{{calculateTime}}s</span>
            <span class="wordClass stateDivSpan">:</span>
            <span class="wordClass">{{calculateNumber}}</span>
        </div>
<!--        <div class="bottomClass">-->
<!--            <div class="evaluate">-->
<!--                <span></span>-->
<!--                <div class="star">-->
<!--                    <el-checkbox-group class="checkBox" v-model="checkList" @change="handleCheckAllChange">-->
<!--                        <el-checkbox label="0"></el-checkbox>-->
<!--                        <el-checkbox label="1"></el-checkbox>-->
<!--                        <el-checkbox label="2"></el-checkbox>-->
<!--                    </el-checkbox-group>-->
<!--                </div>-->
<!--            </div>-->
<!--        </div>-->
<!--        <div class="scoreDiv" v-show="otherState">-->
<!--            <span></span>-->
<!--            <el-input v-model="score" class="scoreInput"></el-input>-->
<!--            <span style="margin-left: 10px">%</span>-->
<!--        </div>-->
        <div class="bottomDiv">
            <el-button class="bottomDivBtn" @click="saveClick"></el-button>
            <el-button class="bottomDivBtn" @click="cancelClick"></el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "RelationAuto",
        computed:{
            uploadURL(){
                // if(process.env.NODE_PATH==='production'){
                //     return '/manage/generate_triple';
                // }
                return '/manage/generate_triple';
            },
        },
        data(){
          return{
              customColor:'rgba(10,135,123,1)',
              fileList: [],
              progressPercent:0,
              progressFlag:false,
              fileStatus:'',
              checkList:[],
              otherState:false,
              score:'',
              attrTableData:[],
              fileState:'',
              isSaveTime:false,
              calculateTime:0,
              rands:0,
              calculateNumber:0,
              isSaving:false,
              isCalculate:false,
          }
        },
        mounted(){
            this.$store.commit('updateTitle','');
            this.$store.commit('updateLoginState',true);

            if(this.$store.state.app.saveRelationAutoData.attrTableData&&this.$store.state.app.saveRelationAutoData.attrTableData.length>0){
                this.checkList=this.$store.state.app.saveRelationAutoData.checkList;
                this.otherState=this.$store.state.app.saveRelationAutoData.otherState;
                this.score=this.$store.state.app.saveRelationAutoData.score;
                this.attrTableData=this.$store.state.app.saveRelationAutoData.attrTableData;
                this.fileState=this.$store.state.app.saveRelationAutoData.fileState;
                this.rands=this.$store.state.app.saveRelationAutoData.rands;
                this.calculateNumber=this.$store.state.app.saveRelationAutoData.calculateNumber;
            }
        },
        beforeDestroy(){
            let temp={
                checkList:this.checkList,
                otherState:this.otherState,
                score:this.score,
                attrTableData:this.attrTableData,
                fileState:this.fileState,
                rands:this.rands,
                calculateNumber:this.calculateNumber,
            }
            this.$store.commit('updateSaveRelationAutoData',temp);
        },
        methods:{
            saveClick(){
                if(this.attrTableData.length>0){
                    this.isSaving=true;
                    // let strLen;
                    // if(this.checkList.length>0){
                    //     if(this.checkList[0]==='0'){
                    //         strLen=100;
                    //     }else if(this.checkList[0]==='1'){
                    //         strLen=0;
                    //     }else{
                    //         strLen=this.score;
                    //     }
                    // }else{
                    //     strLen='';
                    // }
                    this.$axios({
                        method:'get',
                        url:'/manage/generate_triple_submit',
                        params:{
                            // stars:strLen,
                            rands:this.rands
                        },
                    }).then((res)=>{
                        this.isSaving=false;
                        if(res.data.code === 1){
                            this.$message.success('');
                            this.rands=0;
                        }else{
                            this.$message.error('');
                        }
                    })
                }else{
                    this.$message.error('');
                }
            },
            cancelClick(){
                this.fileList=[];
                this.fileState='';
                this.checkList=[];
                this.calculateTime=0;
                this.calculateNumber=0;
                this.attrTableData=[];
            },
            submit(){
                // console.log(this.$refs.upload);
                if(this.$refs.upload.uploadFiles.length>0){
                    this.fileState='';
                    this.isCalculate=true;
                    this.$refs.upload.submit();
                }else{
                    this.$message.error('');
                }
            },
            inputFile(){
                this.cancelClick();
                this.fileList=[];
                this.fileState='';
            },
            handleChange(file, fileList) {

            },
            handleError(file) {
                this.$message.error('');
                // this.isSaving=false;
                // this.fileStatus='';
            },
            handleSuccess(file, fileList) {
                this.isCalculate=false;
                if(file.code===1){
                    let temp=[];
                    for(let item of file.triple){
                        // console.log(item.spo_list);
                        let object={
                            predicate: item.spo_list.predicate,
                            subject: item.spo_list.subject,
                            subject_type: item.spo_list.subject_type,
                            object: item.spo_list.object,
                            object_type: item.spo_list.object_type,
                            get_time:item.date,
                            text:item.text,
                        }
                        temp.push(object)
                    }
                    this.calculateNumber=file.num;
                    this.attrTableData=temp;
                    this.fileState='';
                    this.calculateTime=file.time;
                    this.rands=file.rands;
                }
            },
            uploadProgress(event,file,fileList){

            },
            handleCheckAllChange(value){
                if(value.length>1){
                    this.checkList.shift();
                    this.lastSelect=this.checkList[0];
                }else if(value.length===0){
                    this.checkList.push(this.lastSelect);
                }else{
                    this.lastSelect=this.checkList[0];
                }

                this.otherState = this.checkList[0] === '2';
            },
        }
    }
</script>

<style scoped>
