        height: 100%;
        width: 100%;
    }
    .bottomClass{
        width: 100%;
        height: 10%;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding-left: 40px;
    }
    .evaluate{
        width: 80%;
        height: 30px;
        margin-left: 60px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }
    .evaluate span{
        color: rgba(67,255,226,1);
    }
    .star{
        height: 100%;
        margin-left: 30px;
        padding-top: 4px;
