        /*border-radius: 50%;*/
        background: radial-gradient(circle,rgba(44,186,165,1),rgba(10,135,123,1));
        border: 0;
        box-shadow: #2c3e50 0 0 1px 1px inset;
    }
    .checkBox >>> .el-checkbox__label{
        color: #dddddd;
    }
    .scoreDiv{
        width: 100%;
        height: 40px;
