        width: 60px;
        margin-left: 30px;
    }
    .scoreInput >>> .el-input__inner{
        border: 1px solid rgba(67,255,226,1);
        background:  transparent;
        color: rgba(67,255,226,1);
    }

    .progressDiv{
        height: 25px;
        width: 80%;
