<template>
    <div id="body">
        <div id="graph"></div>
    </div>
</template>

<script>
    import * as d3 from "d3";
    import {
        getNodeSelfPath,
        setLinkGroup,
        getNodesLine,} from '@/assets/js/deviceRelation'
    // import entityData from '@/static/data/entity_file.json'
    import bus from '@/assets/js/bus'

    export default {
        name:'KnowledgeMap',
        props:{
            entityData:{
                type:Array,
            }
        },
        data(){
            return{
                mapData:{},
                oldData:{},
                limit:'',
            }
        },
        beforeDestroy(){
            // let temp=this.deepCopy(this.mapData);
            // temp.links=this.updateSaveMap(temp);
            // this.$store.commit('updateMapSave',temp);
            //
            // let tempOld=this.deepCopy(this.oldData);
            // tempOld.links=this.updateSaveMap(tempOld);
            // this.$store.commit('updateOldMapSave',tempOld);
        },
        mounted() {
            bus.$off('createMap');
            bus.$on('createMap',(val)=>{
                console.log(val);
                this.createMap(val)
                this.renderGraph(this.mapData,this);
            });

            bus.$off('changeLimit');
            bus.$on('changeLimit',(val)=>{
                this.limit=parseInt(val);
            });

            bus.$off('showMap');
            bus.$on('showMap',(val)=>{
                this.mapData=val;
                this.mapData.links=this.updateSaveMap(this.mapData);
                this.oldData=this.$store.state.app.saveMapList[this.$store.state.app.saveMapListPos];
                // console.log(this.mapData);
                this.renderGraph(this.mapData,this);
            });

            bus.$off('showMapBack');
            bus.$on('showMapBack',(val)=>{
                this.mapData=val;
                // let temp=
                this.mapData.links=this.updateSaveMap(this.mapData);
                this.oldData=this.$store.state.app.saveMapList[this.$store.state.app.saveMapListPos];
                this.renderGraph(this.mapData,this);
            });

            bus.$off('createMapRelation');
            bus.$on('createMapRelation',(val)=>{
                this.inputData(val)
                this.renderGraph(this.mapData,this);
            });

            bus.$off('updateMap');
            bus.$on('updateMap',(val)=>{
                this.mapData=val;
                this.mapData.links=this.updateSaveMap(this.mapData);
                this.cleanData(this.mapData);
                this.renderGraph(this.mapData,this);
            });
            // console.log('test');
        },
        methods: {
            //
            deepCopy(obj) {
                if(typeof obj!=='object')return obj;
                let temp=obj instanceof Array?[]:{};
                for(let key in obj){
                    if(obj.hasOwnProperty(key)){
                        temp[key]=typeof obj[key]==='object'?this.deepCopy(obj[key]):obj[key];
                    }
                }
                return temp;
            },
            //
            updateSaveMap(mapData){
                let links=[];
                if(mapData.links){
                    for(let item of mapData.links){
                        let tempLink={
                            "source": 0,
                            "target": 0,
                            "relation": {
                                "relationshipId": 0,
                                "relationship": "",
                                "getDate":'',
                                "isSelf": false
                            }
                        }
                        tempLink.source=(typeof item.source.index!=='undefined')?item.source.index:item.source;
                        tempLink.target=(typeof item.target.index!=='undefined')?item.target.index:item.target;
                        tempLink.relation=item.relation;
                        links.push(tempLink);
                    }
                }
                return links;
            },
            cleanData(data){
                let queen=[];
                this.oldData.links=this.updateSaveMap(this.oldData);
                for(let index of data){
                    for(let item of this.oldData.links){
                        if(index===item.relation.relationship){
                            if(!this.isSourceAndTarget(item.target)){
                                // queen.push(target);
                                queen.push(item.target);
                            }
                            // queen.push(item.target);
                        }
                    }
                }
                function deepCopy(obj) {
                    if(typeof obj!=='object')return obj;
                    let temp=obj instanceof Array?[]:{};
                    for(let key in obj){
                        if(obj.hasOwnProperty(key)){
                            temp[key]=typeof obj[key]==='object'?deepCopy(obj[key]):obj[key];
                        }
                    }
                    return temp;
                }
                let tempOldData=deepCopy(this.oldData);
                while(queen.length>0){
                    let temp=queen.shift();
                    for(let i=0;i<tempOldData.nodes.length;i++){
                        if(tempOldData.nodes[i].id===temp){
                            tempOldData.nodes.splice(i,1);
                            i--;
                            // if(!this.isSourceAndTarget(temp)){
                            //     tempOldData.nodes.splice(i,1);
                            //     i--;
                            // }
                        }
                    }
                    for(let i=0;i<tempOldData.links.length;i++){
                        let source=(typeof tempOldData.links[i].source.index!=='undefined')?tempOldData.links[i].source.index:tempOldData.links[i].source;
                        let target=(typeof tempOldData.links[i].target.index!=='undefined')?tempOldData.links[i].target.index:tempOldData.links[i].target;

                        if(target===temp){
                            // if(!this.isSourceAndTarget(temp)){
                            //     tempOldData.links.splice(i,1);
                            //     i--;
                            // }
                            tempOldData.links.splice(i,1);
                            i--;
                        }
                        if(source===temp){
                            queen.push(target);
                            // if(!this.isSourceAndTarget(temp)){
                            //     queen.push(target);
                            // }
                        }
                    }
                }
                let tempLinks=this.rebuildRelation(tempOldData);

                for(let index of data){
                    for(let i=0;i<tempOldData.links.length;i++){
                        if(index===tempOldData.links[i].relation.relationship){
                            tempOldData.links.splice(i,1);
                            i--;
                        }
                    }
                }
                tempLinks=this.rebuildRelation(tempOldData);

                let retData={
                    nodes:tempOldData.nodes,
                    links:tempLinks
                }
                // console.log(retData);
                this.mapData=retData;
            },
            isSourceAndTarget(id){
                let isSource=false;
                let isTarget=false;
                for(let item of this.oldData.links){
                    let source=(typeof item.source.index!=='undefined')?item.source.index:item.source;
                    let target=(typeof item.target.index!=='undefined')?item.target.index:item.target;
                    if(source===id)isSource=true;
                    if(target===id)isTarget=true;
                }
                return isTarget&&isSource;
            },
            rebuildRelation(tempOldData){
                let tempLinks=[];
                for(let i=0;i<tempOldData.links.length;i++){
                    let source=typeof tempOldData.links[i].source.index!=='undefined'?tempOldData.links[i].source.index:tempOldData.links[i].source;
                    let target=typeof tempOldData.links[i].target.index!=='undefined'?tempOldData.links[i].target.index:tempOldData.links[i].target;
                    let tempLink={
                        "source": 0,
                        "target": 0,
                        "relation": {
                            "relationshipId": 0,
                            "relationship": "",
                            "getDate":'',
                            "isSelf": false
                        }
                    }
                    for(let j=0;j<tempOldData.nodes.length;j++){
                        if(tempOldData.nodes[j].id===source){
                            tempLink.source=j;
                        }
                        if(tempOldData.nodes[j].id===target){
                            tempLink.target=j;
                        }
                    }
                    tempLink.relation=tempOldData.links[i].relation;
                    tempLinks.push(tempLink);
                }
                for(let j=0;j<tempLinks.length;j++){
                    if(tempLinks[j].source===0&&tempLinks[j].target===0){
                        tempLinks.splice(j,1);
                        j--;
                    }
                }
                return tempLinks;
            },
            createMap(data){
                let node=[];
                let link=[];
                let subjectNode={
                    "id":0,
                    "type":0,
                    "label": "Device",
                    "agentName":"",
                }
                // subjectNode.agentName=data[0].source;
                subjectNode.agentName=data;
                node.push(subjectNode);
                this.mapData={
                    "links": link,
                    "nodes":node
                };
                let tempDate=this.deepCopy(this.$store.state.app.saveMapList);
                tempDate.push(this.deepCopy(this.mapData));
                this.$store.commit('updateSaveMapList',tempDate);
                bus.$emit('updateRoute');
            },
            inputData(entityData){
                let node=[];
                let link=[];
                for(let item of entityData){
                    let hasSubject=false;
                    let hasObject=false;
                    let subjectNode={
                        "id":0,
                        "type":0,
                        "label": "Device",
                        "agentName":"",
                    }
                    let objectNode={
                        "id":0,
                        "type":0,
                        "label": "Device",
                        "agentName":"",
                    }
                    for(let i in node){
                        if(node[i].agentName===item.source){
                            hasSubject=true;
                        }
                        if(node[i].agentName===item.target){
                            hasObject=true;
                        }
                    }
                    if(!hasSubject){
                        subjectNode.id=node.length;
                        subjectNode.agentName=item.source;
                        node.push(subjectNode);
                    }
                    if(!hasObject){
                        objectNode.id=node.length;
                        objectNode.agentName=item.target;
                        node.push(objectNode);
                    }
                }
                // console.log(666)
                //
                for(let item of entityData){
                    let sourceNode=item.source;
                    let targetNode=item.target;
                    let tempLine={
                        "source": 0,
                        "target": 0,
                        "relation": {
                            "relationshipId": 0,
                            "relationship": "",
                            "getDate":'',
                            "isSelf": false
                        }
                    }
                    tempLine.relation.relationship=item.rela;
                    tempLine.relation.getDate=item.get_time;
                    for(let i in node){
                        if(node[i].agentName===sourceNode){
                            tempLine.source=parseInt(i);
                        }
                        if(node[i].agentName===targetNode){
                            tempLine.target=parseInt(i);
                        }
                    }
                    link.push(tempLine);
                }
                this.mapData={
                    "links": link,
                    "nodes":node
                };
            },
            renderGraph(data,that) {
                // d3.selectAll('svg').remove();
                d3.select("#graph").html('');

                let {links, nodes} = data;
                let clickFlag =null;
                const circleRadius=35;

                //
                setLinkGroup(links);

                //1.2.3.
                let [relationPath,relationShip1,relationShip2] = [null];
                let [tablePath,tableShow1,tableShow2,tableShow3,tableShow4] = [null];
                let [attrPath,attribute1,attribute2] = [null];

                let rightArc = d3.svg.arc().outerRadius(circleRadius*2).innerRadius(circleRadius+1).startAngle(0).endAngle(2.09);
                let bottomArc = d3.svg.arc().outerRadius(circleRadius*2).innerRadius(circleRadius+1).startAngle(2.10).endAngle(4.19);
                let leftArc = d3.svg.arc().outerRadius(circleRadius*2).innerRadius(circleRadius+1).startAngle(4.2).endAngle(6.27);

                let force = d3.layout.force()//layoutjson
                    .nodes(nodes)//
                    .links(links)//
                    .linkDistance(150)//
                    .charge(-3000)//
                    .on("tick", tick)//
                    .start();//

                let zoom = d3.behavior.zoom()
                    .scaleExtent([.4, 2])
                    .on("zoom", zoomed);

                let svg = d3.select("#graph").append("svg")
                    .attr("pointer-event", "all")
                    .attr("preserveAspectRatio", "xMidYMid meet")//
                    .attr("viewBox", "-500 -200 1500 1500")
                    .call(zoom);

                //
                let edges_line = svg.append("g").selectAll(".edgepath")
                    .data(force.links())
                    .enter()
                    .append("path")
                    .attr("marker-end",  function (d, i) { return getMarkerArrow(i); })//id
                    .style("stroke", 'rgba(67,255,226,1)')
                    .style("stroke-width", 1)//
                    .style("fill-opacity",0)
                    .style("cursor","pointer")
                    .attr("id", function (d, i) { return 'edgepath' + i; })
                    .on("mouseover", function(d){ return getStrokeWidth(d); })
                    .on("mouseout", function() { edges_line.style("stroke-width", 1) })
                    .on('click', (d, i) => { showInfoLine(d, i); });

                let edges_text = svg.append("g").selectAll(".edgetext")
                    .data(links)
                    .enter()
                    .append("text")
                    // .style("pointer-events", "none")
                    .attr("class","linetext")
                    .attr('text-anchor', "middle")
                    .attr("fill-opacity", 1)
                    .style("cursor","pointer")
                    .attr({'class': 'edgelabel', 'id': function (d, i) { return 'edgepath' + i; }, 'dx': 50, 'dy': -5, 'fill':'rgba(67,255,226,1)'})
                    .on("click", (node, i) => {
                        showInfoLine(node, i);
                    });

                let edges_text2 = svg.append("g").selectAll(".edgetext")
                    .data(links)
                    .enter()
                    .append("text")
                    // .style("pointer-events", "none")
                    .attr("class","linetext")
                    .attr('text-anchor', "middle")
                    .attr("fill-opacity", 1)
                    .attr({'class': 'edgelabel', 'id': function (d, i) { return 'edgepath' + i; }, 'dx': 50, 'dy': 12, 'fill':'rgba(67,255,226,1)'})
                    .on("click", (node, i) => {
                        showInfoLine(node, i);
                    });

                //
                edges_text.append('textPath')
                    .style("cursor","pointer")
                    .attr('xlink:href', function (d, i) { return '#edgepath' + i })
                    // .attr("pointer-events", "none")
                    .attr("font-size", 12)
                    .text(function (d) { return d.relation.relationship; });

                edges_text2.append('textPath')
                    .style("cursor","pointer")
                    .attr('xlink:href', function (d, i) { return '#edgepath' + i })
                    // .attr("pointer-events", "none")
                    .attr("font-size", 8)
                    .text(function (d) {if(typeof d.relation.getDate==='string') return d.relation.getDate;return "";});

                let circle_g = svg.selectAll("circle")
                    .data(force.nodes())//force.nodes
                    .enter()
                    .append("g")
                    .attr("class",function(node,i){ return "g_circle_" + i; })//circle
                    .style("cursor","pointer")
                    .attr('fixed',false)
                    .call(drag()) //drag
                    // .call((node,i)=>{showAllArc(node,i,this)})
                    .on("click", (node, i) => {
                        if (d3.event.defaultPrevented) return;//click
                        // console.log(node)
                        if(node.type===0){//
                            circleOneClick(node, i, this);
                        }
                    })
                    .on('mouseover', (node) => {
                        if (d3.event.defaultPrevented) return;
                        showNodeInfo(node, this);
                    });

                svg.on("dblclick.zoom", null);//svgd37
                circle_g.on("dblclick.zoom", null);
                circle_g.on('dblclick',(node,i)=>{
                    if(node.type===0)circleDoubleClick(node,i,this);
                });

                function getLineTextDx(d) {
                    // const sr = nodeConf.radius[d.source.ntype];
                    // console.log(d);
                    const sx = d.source.x;
                    const sy = d.source.y;
                    const tx = d.target.x;
                    const ty = d.target.y;

                    // console.log(d.source);
                    // console.log(d.source.x);
                    // console.log(sx);
                    const distance = Math.sqrt(Math.pow(tx - sx, 2) + Math.pow(ty - sy, 2));
                    // console.log(d.source);
                    // console.log(d.target.x);
                    // console.log(d.target.y);
                    // const textLength = d.labels.length;
                    const deviation = 0; // 
                    return (distance - circleRadius*2 - d.relation.relationship.length) / 2 + deviation;

                    // return dx;
                }

                //
                function circleOneClick(node, i, _this) {
                    // console.log(node);
                    if(clickFlag) {//
                        clickFlag = clearTimeout(clickFlag);
                    }

                    clickFlag = setTimeout(function() {
                        // click 
                        //
                        let existedRing = svg.select('.g_circle_' + i).selectAll('.g_circle_path');
                        //
                        svg.selectAll('.g_circle_path').remove();
                        svg.selectAll('.remove_a').remove();
                        svg.selectAll('.hide_a').remove();
                        svg.selectAll('.unlock_a').remove();
                        if(existedRing && existedRing[0].length === 0) {
                            //
                            showAllArc(node,i,_this);
                        }
                    }, 200);//200
                }

                //
                // eslint-disable-next-line no-unused-vars
                function circleDoubleClick(node,i,_this) {
                    if(clickFlag) {//
                        clickFlag = clearTimeout(clickFlag);
                    }
                    // dblclick 
                    // console.log(i);
                    node['sendType']='circle';
                    that.$router.push({
                        path:'/Trace',
                        query: {'node': JSON.stringify(node),'limit':that.limit} // 
                    })
                }

                //
                let circle = circle_g.append("circle")
                    .style("stroke-width", "2px")
                    .attr("r", circleRadius)//
                    .style("fill", function (node) { return getCircleColor(node); });

                let text = circle_g.append("text")
                    .attr("dy", ".25em")
                    .attr("text-anchor", "middle")//
                    .style('fill', "#FFFFFF")
                    .attr('x',function(d){ appendCircleText(d, this); });

                //
                circle.append("svg:title").text(function(node) {
                    return node.agentName;
                });
                text.append("svg:title").text(function(node) {
                    return node.agentName;
                });

                function showAllArc(node,i,_this) {
                    showRelationShip(node, i, _this);
                    showTable(node, i, _this);
                    showAttribute(node,i, _this);
                }

                function zoomed() {//svgg
                    svg.selectAll("g").attr("transform", "translate("  +d3.event.translate + ")scale(" +d3.event.scale + ")");
                }

                function getMarkerArrow(i) {
                    svg.append("defs").append("marker")   //
                        .attr("id", "arrow"+i)
                        .attr("markerUnits","strokeWidth")//strokeWidth
                        .attr("markerUnits", "userSpaceOnUse")
                        .attr("markerWidth", 10)//
                        .attr("markerHeight", 10)
                        .attr("viewBox", "0 -4 12 15")//
                        .attr("refX", 0)//
                        .attr("refY", 0)
                        .attr("orient", 'auto')//auto 
                        .append("svg:path")
                        .attr("stroke-width",1)//
                        .attr("d", "M0,-5L10,0L0,5")//
                        .attr('fill', 'rgba(67,255,226,1)');//
                    return "url(#arrow" + i + ")";
                }

                function getStrokeWidth(d) {
                    edges_line.style("stroke-width", function (edge) {
                        return edge.relation.relationshipId === d.relation.relationshipId ? 5 : 1;
                    });
                }

                function drag(){//
                    return force.drag().on("dragstart",function(d,i){
                        d3.event.sourceEvent.stopPropagation(); //
                        // eslint-disable-next-line no-unused-vars
                        let existedRing = svg.select('.g_circle_' + i);
                        for(let item of force.nodes()){
                            item.fixed=true;
                        }
                        d.fixed = true;//
                    });
                }

                function getCircleColor() {
                    // let color = {
                    //   'Device': "#2c3e50",
                    // };//
                    return "rgba(27,105,94,0.8)";
                }

                function appendCircleText(d, _this) {
                    let circleText = '';
                    let mainText='';
                    let viceText='';
                    circleText=d.agentName;
                    //
                    if(circleText && circleText.length > 6){
                        viceText=circleText.substring(5,circleText.length);
                        if(viceText&&viceText.length>5){
                            viceText=viceText.substring(0,4)+'...'
                        }
                        mainText = circleText.substring(0,5);
                        d3.select(_this).text(function(){return '';});
                    }
                    if(circleText && circleText.length <= 6){
                        d3.select(_this).append('tspan').attr('x',0).attr('y',0).attr("font-size", 10)
                            .text(function(){ return circleText; });
                    }else{
                        d3.select(_this).append('tspan').attr('x',0).attr('y',-6).attr("font-size", 10)
                            .text(function(){ return mainText; });
                        d3.select(_this).append('tspan').attr('x',0).attr('y',10).attr("font-size", 10)
                            .text(function(){ return viceText; })
                    }
                }

                // eslint-disable-next-line no-unused-vars
                function showInfoLine(d, i) {
                    // alert('delete this '+ d + ',' + i);
                    // console.log(d);
                    d['sendType']='line';
                    bus.$emit('showInfoLine',d);
                    // that.$router.push({
                    //     path:'/Trace',
                    //     query: {'node': JSON.stringify(d)} // 
                    // })
                }

                function tick() {
                    circle.attr("transform", transform1);//
                    text.attr("transform", transform2);//

                    edges_line.attr('d', function (d) {
                        //
                        // console.log(d);
                        if(d.relation.isSelf) { return getNodeSelfPath(d) }
                        //
                        return getNodesLine(d);//
                    });
                    edges_text.attr('dx', function(d){
                        return getLineTextDx(d);
                    });

                    edges_text2.attr('dx', function(d){
                        return getLineTextDx(d);
                    });

                    //
                    if(relationPath !== null) {
                        relationPath.attr("transform", function (d){ return "translate("+d.x+","+d.y+")" });

                        let r1 = null;
                        relationShip1.attr('d',function(d) { r1 = d; })
                            .attr("x",r1.x-5).attr("y",r1.y - (circleRadius+10)).attr('transform-origin',r1.x+' '+r1.y);
                        relationShip2.attr('d',function(d) { r1 = d; })
                            .attr("x",r1.x-5).attr("y",r1.y - (circleRadius+10)).attr('transform-origin',r1.x+' '+r1.y);
                    }

                    //
                    if(tablePath !== null) {
                        tablePath.attr("transform", function (d){ return "translate("+d.x+","+d.y+")" });

                        let r1 = null;
                        tableShow1.attr('d',function(d) { r1 = d; })
                            .attr("x",r1.x-5).attr("y",r1.y + (circleRadius+20)).attr('transform-origin',(r1.x)+' '+(r1.y));
                        tableShow2.attr('d',function(d) { r1 = d; })
                            .attr("x",r1.x-5).attr("y",r1.y + (circleRadius+20)).attr('transform-origin',r1.x+' '+r1.y);
                        tableShow3.attr('d',function(d) { r1 = d; })
                            .attr("x",r1.x-5).attr("y",r1.y + (circleRadius+20)).attr('transform-origin',r1.x+' '+r1.y);
                        tableShow4.attr('d',function(d) { r1 = d; })
                            .attr("x",r1.x-5).attr("y",r1.y + (circleRadius+20)).attr('transform-origin',r1.x+' '+r1.y);

                    }

                    //
                    if(attrPath !== null) {
                        attrPath.attr("transform", function (d){ return "translate("+d.x+","+d.y+")" });

                        let r1 = null;
                        attribute1.attr('d',function(d) { r1 = d; })
                            .attr("x",r1.x-5).attr("y",r1.y - (circleRadius+10)).attr('transform-origin',r1.x+' '+r1.y);
                        attribute2.attr('d',function(d) { r1 = d; })
                            .attr("x",r1.x-5).attr("y",r1.y - (circleRadius+10)).attr('transform-origin',r1.x+' '+r1.y);

                    }

                }

                //
                function transform1(d) {
                    return "translate(" + d.x + "," + d.y + ")";
                }

                function transform2(d) {
                    return "translate(" + (d.x) + "," + d.y + ")";
                }
                function showRelationShip(node, i, _this) {
                    relationPath = d3.select('.g_circle_'+ i).append("path").attr('class', 'g_circle_path')
                        .attr("transform", "translate(" + node.x + "," + node.y + ")").attr("d", rightArc)
                        .attr("fill", "rgba(27,105,94,0.6)")
                        .style("cursor","pointer")
                        .on("click", () => { showRelationship(node,i, _this); })
                        .on("mouseover", function(){ relationPath.attr("fill", "rgba(27,105,94,0.6)") })
                        .on("mouseout", function() { relationPath.attr("fill", "rgba(27,105,94,0.6)") });

                    d3.select('.g_circle_'+ i).append("g").attr("class","remove_a")
                        .attr("transform", "translate(" + (node.x + 33) + "," + (node.y - 24) + ") scale(0.7)")
                        .append("defs").append("style")
                        .text(".remove_a{stroke:#FFFFFF;font-size:10px;font-family:STXihei;font-weight:100;stroke-width:0.7px;fill:none;transform:rotate(45deg);}");

                    relationPath.append("title").attr("class","remove_a").text("relation");

                    let r1=null;
                    relationShip1 = d3.select('.g_circle_'+ i).append('text').text('')
                        .attr("class","remove_a")
                        .attr('d',function(d) { r1 = d; })
                        .attr("x",r1.x-5).attr("y",r1.y - (circleRadius+10))
                        .attr('transform-origin',r1.x+' '+r1.y)

                    relationShip2 = d3.select('.g_circle_'+ i).append('text').text('')
                        .attr("class","remove_a")
                        .style('transform','rotate(70deg)')
                        .attr('d',function(d) { r1 = d; })
                        .attr("x",r1.x-5).attr("y",r1.y - (circleRadius+10))
                        .attr('transform-origin',r1.x+' '+r1.y)

                    d3.selectAll(".remove_a").on("click", () => { showRelationship(node, i,_this); })
                }

                function showTable(node,i) {
                    attrPath = d3.select('.g_circle_'+ i).append("path").attr('class', 'g_circle_path')
                        .attr("transform", "translate(" + node.x + "," + node.y + ")").attr("d", bottomArc)
                        .attr("fill", "rgba(27,105,94,0.6)")
                        .style("cursor","pointer")
                        .on("click", function(){ showTableClick(node,i) })
                        .on("mouseover", function(){ attrPath.attr("fill", "rgba(27,105,94,0.6)") })
                        .on("mouseout", function() { attrPath.attr("fill", "rgba(27,105,94,0.6)") });

                    d3.select('.g_circle_'+ i).append("g").attr("class","hide_a")
                        .attr("transform", "translate(" + (node.x - 8) + "," + (node.y + 38) + ") scale(0.7)")
                        .append("defs").append("style")
                        .text(".hide_a{stroke:#FFFFFF;font-size:10px;font-family:STXihei;font-weight:100;stroke-width:0.7px;fill:none;transform:rotate(30deg);}");

                    attrPath.append("title").attr("class","hide_a").text("show table");

                    let r1=null;
                    tableShow1 = d3.select('.g_circle_'+ i).append('text').text('')
                        .attr("class","hide_a")
                        .attr('d',function(d) { r1 = d; })
                        .attr("x",r1.x-5).attr("y",r1.y + (circleRadius+20))
                        .attr('transform-origin',r1.x+' '+r1.y)

                    tableShow2 = d3.select('.g_circle_'+ i).append('text').text('')
                        .attr("class","hide_a")
                        .style('transform','rotate(10deg)')
                        .attr('d',function(d) { r1 = d; })
                        .attr("x",r1.x-5).attr("y",r1.y + (circleRadius+20))
                        .attr('transform-origin',r1.x+' '+r1.y)

                    tableShow3 = d3.select('.g_circle_'+ i).append('text').text('')
                        .attr("class","hide_a")
                        .style('transform','rotate(-10deg)')
                        .attr('d',function(d) { r1 = d; })
                        .attr("x",r1.x-5).attr("y",r1.y + (circleRadius+20))
                        .attr('transform-origin',r1.x+' '+r1.y)

                    tableShow4 = d3.select('.g_circle_'+ i).append('text').text('')
                        .attr("class","hide_a")
                        .style('transform','rotate(-30deg)')
                        .attr('d',function(d) { r1 = d; })
                        .attr("x",r1.x-5).attr("y",r1.y + (circleRadius+20))
                        .attr('transform-origin',r1.x+' '+r1.y)

                    d3.selectAll(".hide_a").on("click", (d,i) => { showTableClick(d,i); })
                }
                function showAttribute(node, i) {
                    tablePath = d3.select('.g_circle_'+ i).append("path").attr('class', 'g_circle_path')
                        .attr("transform", "translate(" + node.x + "," + node.y + ")").attr("d", leftArc)
                        .attr("fill", "rgba(27,105,94,0.6)").style("cursor","pointer")
                        .on("click", function(d,i){ attributeShow(d,i); })
                        .on("mouseover", function(){ tablePath.attr("fill", "rgba(27,105,94,0.6)") })
                        .on("mouseout", function() { tablePath.attr("fill", "rgba(27,105,94,0.6)") });

                    d3.select('.g_circle_'+ i).append("g").attr("class","unlock_a")
                        .attr("transform", "translate(" + (node.x - 52) + "," + (node.y - 30) + ") scale(0.7)")
                        .append("defs").append("style")
                        .text(".unlock_a{stroke:#FFFFFF;font-size:10px;font-family:STXihei;font-weight:100;stroke-width:0.6px;fill:none;transform:rotate(280deg);}");

                    tablePath.append("title").attr("class","unlock_a").text("Attribute");

                    let r1=null;
                    attribute1 = d3.select('.g_circle_'+ i).append('text').text('')
                        .attr("class","unlock_a")
                        .attr('d',function(d) { r1 = d; })
                        .attr("x",r1.x-5).attr("y",r1.y - (circleRadius+10))
                        .attr('transform-origin',r1.x+' '+r1.y)
                    attribute2 = d3.select('.g_circle_'+ i).append('text').text('')
                        .attr("class","unlock_a")
                        .style('transform','rotate(310deg)')
                        .attr('d',function(d) { r1 = d; })
                        .attr("x",r1.x-5).attr("y",r1.y - (circleRadius+10))
                        .attr('transform-origin',r1.x+' '+r1.y)

                    d3.selectAll(".unlock_a").on("click", (d,i) => { attributeShow(d,i); })

                }
                function showTableClick(node,i) {
                    console.log(i);
                    d3.event.stopPropagation();
                    console.log(node.agentName)
                    that.$router.push({
                        path:'/TableShow',
                        query: {'node': node.agentName,'limit':that.limit} // 
                    })
                }

                function showRelationship(node, i,_this){
                    d3.event.stopPropagation();
                    // that.updateMap(node,i);

                    that.$axios({
                        method:'get',
                        url:'/navigation/search/entity_relationship',
                        params:{
                            entity:node.agentName,
                            limit:that.limit
                        }
                    }).then((res)=>{
                        if(res.data.code === 1){
                            that.addNode(res,that,node,i,0,1);
                        }
                    })
                }
                function showNodeInfo(node, _this) {
                    // window.console.log(node);
                    // window.console.log(_this);

                }
                // eslint-disable-next-line no-unused-vars
                function attributeShow(d,i) {
                    console.log(i);
                    d3.event.stopPropagation();

                    that.$axios({
                        method:'get',
                        url:'/navigation/search/entity_atribute',
                        params:{
                            entity:d.agentName,
                            limit:that.limit
                        }
                    }).then((res)=>{
                        if(res.data.code === 1){
                            that.addNode(res,that,d,i,1,1);
                        }
                    })
                    // d3.select(this).classed("fixed", d.fixed = false);//
                    // //
                    // svg.selectAll('.g_circle_path').remove();
                    // svg.selectAll('.remove_a').remove();
                    // svg.selectAll('.hide_a').remove();
                    // svg.selectAll('.unlock_a').remove();
                }
            },
            //
            addNode(res,that,node,i,type,showMapType){
                function deepCopy(obj) {
                    if(typeof obj!=='object')return obj;
                    let temp=obj instanceof Array?[]:{};
                    for(let key in obj){
                        if(obj.hasOwnProperty(key)){
                            temp[key]=typeof obj[key]==='object'?deepCopy(obj[key]):obj[key];
                        }
                    }
                    return temp;
                }

                let drawTemp={
                    nodes:[],
                    links:[],
                };

                let checkList=[];
                if(type===0){
                    for(let i=0;i<that.mapData.nodes.length;i++){
                        if(that.mapData.nodes[i].type===1){
                            that.mapData.nodes.splice(i,1);
                            i--;
                        }
                    }
                    that.mapData.links=that.rebuildRelation(that.mapData);
                }else{
                    for(let i=0;i<that.mapData.nodes.length;i++){
                        if(that.mapData.nodes[i].type===0&&that.mapData.nodes[i].agentName!==node.agentName){
                            that.mapData.nodes.splice(i,1);
                            i--;
                        }
                    }
                    that.mapData.links=that.rebuildRelation(that.mapData);
                }
                // console.log(that.mapData.links);

                for(let item of that.mapData.nodes){
                    let subjectNode={
                        "id":0,
                        "type":0,
                        "label": "Device",
                        "agentName":"",
                    }
                    subjectNode.type=item.type;
                    subjectNode.id=drawTemp.nodes.length;
                    subjectNode.agentName=item.agentName;
                    drawTemp.nodes.push(subjectNode);
                }
                for(let item of that.mapData.links){
                    let link={
                        "source": 0,
                        "target": 0,
                        "relation": {
                            "relationshipId": 0,
                            "relationship": "",
                            "getDate":'',
                            "isSelf": false
                        }
                    }
                    link.source=typeof item.source.index!=='undefined'?item.source.index:item.source;
                    link.target=typeof item.target.index!=='undefined'?item.target.index:item.target;
                    link.relation.relationship=item.relation.relationship;
                    link.relation.getDate=item.relation.getDate;
                    drawTemp.links.push(link);
                }
                let iPos=0;
                for(let pos of drawTemp.nodes){
                    if(pos.agentName===node.agentName){
                        i=iPos;
                        break;
                    }
                    iPos++;
                }
                let tempData=res.data.triple;
                let hasChange=false;
                for(let item of tempData){
                    if(node.agentName===item.source){
                        let flags=true;
                        for(let index of that.mapData.nodes){
                            if(index.agentName===item.target){
                                flags=false;
                                break;
                            }
                        }
                        if(flags){
                            hasChange=true;
                            let subjectNode={
                                "id":0,
                                "type":type,
                                "label": "Device",
                                "agentName":"",
                            }
                            subjectNode.id=drawTemp.nodes.length;
                            subjectNode.agentName=item.target;
                            let link={
                                "source": 0,
                                "target": 0,
                                "relation": {
                                    "relationshipId": 0,
                                    "relationship": "",
                                    "getDate":'',
                                    "isSelf": false
                                }
                            }
                            // console.log(i);
                            link.source=i;
                            drawTemp.nodes.push(subjectNode);
                            link.target=drawTemp.nodes.length-1;
                            link.relation.getDate=item.get_time;
                            link.relation.relationship=item.rela;
                            // checkList.push(item.rela);
                            drawTemp.links.push(link);
                        }
                    }
                    else if(node.agentName===item.target){
                        let flags=true;
                        for(let index of that.mapData.nodes){
                            if(index.agentName===item.source){
                                flags=false;
                                break;
                            }
                        }
                        if(flags){
                            hasChange=true;
                            let subjectNode={
                                "id":0,
                                "type":type,
                                "label": "Device",
                                "agentName":"",
                            }
                            subjectNode.id=drawTemp.nodes.length;
                            subjectNode.agentName=item.source;
                            let link={
                                "source": 0,
                                "target": 0,
                                "relation": {
                                    "relationshipId": 0,
                                    "relationship": "",
                                    "getDate":'',
                                    "isSelf": false
                                }
                            }
                            // console.log(i);
                            link.target=i;
                            drawTemp.nodes.push(subjectNode);
                            link.source=drawTemp.nodes.length-1;
                            link.relation.getDate=item.get_time;
                            link.relation.relationship=item.rela;
                            // checkList.push(item.rela);
                            drawTemp.links.push(link);
                        }
                    }
                }
                for(let item of drawTemp.links){
                    checkList.push(item.relation.relationship);
                }
                // console.log(drawTemp);
                if(hasChange){
                    let promise=new Promise((resolve, reject) => {
                        that.mapData=deepCopy(drawTemp);
                        that.oldData=deepCopy(drawTemp);

                        let tempDate=deepCopy(that.$store.state.app.saveMapList);
                        tempDate.push(deepCopy(drawTemp))
                        that.$store.commit('updateSaveMapList',tempDate);
                        if(showMapType&&showMapType===1){
                            let tempPos=that.$store.state.app.saveMapList.length-1;
                            that.$store.commit('updateSaveMapListPos',tempPos);
                        }
                        // console.log(that.$store.state.app.saveMapList);
                        bus.$emit('updateRoute');

                        bus.$emit('addCheckList',checkList);
                        resolve();
                    })

                    promise.then(()=>{
                        that.renderGraph(deepCopy(that.mapData),that);
                    }).catch(()=>{})
                }
            },
        }
    }
</script>

<style>
    #graph {
        width: 100vw;
        height: 100vh;
    }
</style>
