<template>
    <div class="sliderDiv" :class="$store.state.app.slider.opened ?'sliderOpen':'sliderClose'">
        <div class="topShadow"></div>
        <div class="rightShadow"></div>
        <div class="bottomShadow"></div>
        <div class="leftShadow"></div>
        <div class="innerDiv">
            <div class="sliderDivTop">
                <img src="../../assets/img/large/1_large.png" class="sliderImage" alt=""/>
                <i class="sliderDivMore el-icon-more" @click="sliderMore"></i>
            </div>
            <el-menu :default-active="$route.path"
                     class="el-menu-vertical-demo"
                     router
                     background-color="transparent">
                <template v-for="(item,index) in menuData">
                    <el-submenu :index="index+''" v-if="!item.leaf&&!item.hidden" class="subMenu" :key="index">
                        <!---->
                        <template slot="title" class="item-span">
                            <i :class="$t(item.icon)" class="menuIcon2"></i>
                            <span>
                                {{$t(item.name)}}
                            </span>
                        </template>
                        <!---->
                        <template v-for="child in item.children">
                            <el-menu-item v-if="!child.hidden" :index="child.path" :key="child.path">
                            <span>
                                {{$t(child.name)}}
                            </span>
                            </el-menu-item>
                        </template>
                    </el-submenu>
                    <!---->
                    <el-menu-item  v-if="item.leaf&&item.children.length>0&&!item.children[0].hidden" :index="item.children[0].path" :key="index">
                        <i :class="$t(item.icon)" class="menuIcon"></i>
                        <span>{{$t(item.children[0].name)}}</span>
                    </el-menu-item>
                </template>
            </el-menu>
        </div>
        <div class="sliderBtnDiv" @click="sliderClick" :class="$store.state.app.slider.opened ?'sliderBtnOpen':'sliderBtnClose'">
            <i class="sliderBtn el-icon-caret-left"></i>
        </div>
    </div>
</template>

<script>
    import store from "@/store";

    export default {
        name: "Slider",
        data(){
            return {
                menuArr:'',
                menuData:'',
                lock:'',
                choseNum:0,
            }
        },
        methods:{
            showMenuFun(){
                this.menuData = this.$router.options.routes;
            },
            sliderMore(){

            },
            sliderClick(){
                this.$store.commit('TOGGLE_SIDEBAR');
            },
        },
        mounted(){
            this.showMenuFun();
            // console.log(this.$route)
        }
    }
</script>

<style scoped>
    .sliderDiv{
        width: 180px;/*no*/
        height: calc(100% - 20px);
        position: fixed;
        top: 10px;/*no*/
        left: 10px;
        font-size: 16px;/*no*/
        z-index: 500;
        transition:left .6s;
    }
    .sliderOpen{
        left: 10px;
    }
    .sliderClose{
        left: -190px;
    }
    .sliderBtnOpen{
        transition: .6s;
    }
    .sliderBtnClose{
        right: -24px!important;
        transform:  perspective(2em) rotateY(-160deg)!important;
        transition: .6s;
    }
    /*.menuStyle{*/
    /*    width: 30%;*/
    /*    height: 70%;*/
    /*    border-bottom: 2px solid rgba(0,167,155,1);*/
    /*}*/
    .topShadow{
        position: absolute;
        width: 100%;
        height: 2px;
        top: 0;
        left: 0;
        background: -webkit-linear-gradient(left,rgba(0,167,155,1),rgba(3,58,53,1) 30%);
    }
    .rightShadow{
        position: absolute;
        width: 2px;
        height: 100%;
        top: 0;
        right: 0;
        background: -webkit-linear-gradient(bottom,rgba(0,167,155,1),rgba(3,58,53,1) 5%);
    }
    .bottomShadow{
        position: absolute;
        width: 100%;
        height: 2px;
        bottom: 0;
        left: 0;
        background: -webkit-linear-gradient(right,rgba(0,167,155,1),rgba(3,58,53,1) 30%);
    }
    .leftShadow{
        position: absolute;
        width: 2px;
        height: 100%;
        top: 0;
        left: 0;
        background: -webkit-linear-gradient(top,rgba(0,167,155,1),rgba(3,58,53,1) 5%);
    }
    .sliderBtnDiv{
        position: absolute;
        width: 10px;
        height: 20%;
        top: 40%;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        border: 1px solid rgba(7,85,77,1);
        transform:  perspective(2em) rotateY(-20deg);
        padding-right: 2px;
    }
    .sliderBtnDiv:hover{
        cursor: pointer;
    }
    .sliderBtn{
        color: rgba(67,255,226,1);
    }
    .innerDiv{
        width:calc(100% - 4px);
        height:calc(100% - 4px);
        position:absolute;
        top: 2px;
        left: 2px;
        background: -webkit-linear-gradient(top left,rgba(7,30,27,1),rgba(11,87,79,1));
    }

    .sliderDiv .el-menu{
        border-right: none;
        text-align: center;
    }
    .sliderDiv .el-submenu .el-menu-item{
        font-size: 16px!important;
        min-width: 60px!important;
        width: 60px!important;
        padding: 0!important;
        margin-left: 48% !important;
    }
    .menuIcon{
        position: absolute;
        top: 12px;
        left: -25px;
        color: #dddddd;
        font-size: 18px;
    }
    .menuIcon2{
        position: absolute;
        top: 20px;
        left: calc(35% - 25px);
        color: #dddddd;
        font-size: 18px;
    }
    .sliderDiv >>>.el-menu-item{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 15px;
        height: 40px;
        width: auto;
        /*padding-left: 0!important;*/
        margin-left: 35%;
        margin-right: 35%;
        /*overflow: hidden;*/
        text-overflow:ellipsis;
        white-space: nowrap;
        color: white;
        font-size: 16px;
        background-color: transparent!important;
    }

    .sliderDiv >>> .el-menu-item.is-active {
        font-size: 16px;
        border-bottom: 2px solid rgba(0,167,155,1);
        background: transparent;
    }
    .sliderDivTop{
        height: 70px;
        width: 100%;
        display: flex;
        justify-content: space-between;
    }
    .sliderDivTop .sliderImage{
        height: 40px;
        width: 40px;
        margin-left: 10px;
        margin-top: 5px;
        /*background: linear-gradient(#2a3138,#1e2328);*/
        display: block;
    }

    .sliderDiv >>>.el-submenu__title{
        /*overflow: hidden;*/
        text-overflow:ellipsis;
        white-space: nowrap;
        color: white;
        font-size: 16px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .sliderDiv >>>.el-submenu__title:hover{
        background-color: transparent!important;
    }
    .sliderDiv >>>.el-menu-item:hover{
        border-bottom: 2px solid rgba(0,167,155,1);
    }
    .sliderDiv >>>.el-submenu__icon-arrow{
        right: 10px;
    }
    .sliderDivMore{
        transform: rotate(90deg);
        transform-origin: 0 0;
        margin-top: 5px;
        margin-right: -20px;
        font-size: 22px;
        color: rgba(27,107,95,1);
    }
    .sliderDivMore:hover{
        cursor: pointer;
    }

    @media only screen and (max-width: 767px){
        .sliderDiv .el-menu-item,.sliderDiv >>>.el-submenu__title{
            font-size: 12px;
        }
        .sliderDiv >>>.el-menu-item{
            height: 45px;
            line-height: 45px;
        }
        .sliderDiv >>>.el-submenu__title{
            height: 45px;
            line-height: 45px;
        }
        /*.sliderDiv{*/
        /*    background: -webkit-gradient(linear, 100% 0%, 0% 100%,from(#ffffff), to(#f3f6ff));*/
        /*    background: linear-gradient(to  right, #ffffff , #f3f6ff);*/
        /*    background: -moz-linear-gradient( right, #ffffff, #f3f6ff);*/
        /*    background: -o-linear-gradient( right, #ffffff, #f3f6ff);*/
        /*    background: -webkit-linear-gradient(left , #f3f6ff, #fff);*/
        /*}*/
    }
</style>