<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>

import bus from './assets/js/bus'
import store from "./store";
import Home from "@/components/Home/Home";

export default {
  name: 'App',
  components:{
    Home,
  },
  data(){
    return{
      pageShow:true,
    }
  },
  mounted(){
    //  
    bus.$off('getLangFun');
    bus.$on('getLangFun',(val)=>{
      // this.getI18n(val);
      this.changeLangType(val);
    })
  },
  methods:{
    //
    changeLangType(lang){
      //store
      store.commit('updateLangType',lang);
      //store.commit('updateTitle',this.$t('language.scientific_research_competitiveness_title'));
      bus.$emit('sendLangType',lang);
      bus.$emit('sendStoreLang');
    },
  }
}
</script>

